import React from "react";
import styled from "styled-components";
import AddButton from "./AddButton";
import FilterButtons from "./FilterButtons";

const Container = styled.div`
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 40px;
`;

const SearchFilterFields = ({ selectedTag, onSwitchTag }) => {
  return (
    <Container>
      <AddButton />
      <FilterButtons selectedTag={selectedTag} onSwitchTag={onSwitchTag} />
    </Container>
  );
};

export default SearchFilterFields;
