import React, { useEffect, useState } from "react";
import { Modal, Button, Form, Input } from "semantic-ui-react";
import styled from "styled-components";
import { refreshCurrentPage } from "../../utils/pathUtils";
import { gql } from "apollo-boost";
import BasicForm from "../../common/BasicForm";
import { clickEle } from "../../utils/elementLink";
import { useMutation } from "@apollo/react-hooks";
import { OPERATE_RESULT } from "../../common/props";
import _get from "lodash/get";
import _split from "lodash/split";
import ModalHint from "../../common/ModalHint";
import CaptchaInput from "../../common/formFields/CaptchaInput";
import isEmail from "validator/lib/isEmail";
import { ACTION_TYPE, CAPTCHA_TYPE } from "../../common/CONSULT";
import isPhone from "validator/lib/isMobilePhone";
import ToastHint from "../../common/ToastHint";
import { login } from "./LoginModal";

const REGISTER_USER = gql`
    mutation ($username:String!,$password:String!,$phone:String,$email:String,$verificationCode:String){
        registerUser(username:$username,password:$password,phone:$phone,email:$email,verificationCode:$verificationCode) {
            ${OPERATE_RESULT}
        }
    }
`;

const RegisterModal = ({ children, open, onHide, setAuthentication }) => {
  let form = {};
  const [registerUser, { data, loading }] = useMutation(REGISTER_USER, {
    onCompleted: ({ registerUser: { success, message } }) => {
      if (success) {
        const [username, password] = _split(message, ":");
        ToastHint.showSuccess("");
        login(username, password, setAuthentication).catch(error => {
          ToastHint.showFailure("", {
            callback: () => refreshCurrentPage()
          });
        });
      }
    },
    onError: error => ModalHint.error(error.toString())
  });
  console.log("isEmail________________________", isEmail("1299362885@qq.com"));
  console.log("isEmail________________________form", isEmail(form.name || ""));
  return (
    <Modal
      onClose={onHide}
      open={open}
      closeIcon={"close"}
      trigger={children}
      size={"tiny"}
      style={{ top: "300px" }}
    >
      <Modal.Header>LazyChun</Modal.Header>
      <Modal.Content>
        <BasicForm
          loading={loading}
          width={"100%"}
          formData={form}
          items={[
            {
              label: "",
              fieldKey: "username",
              required: true,
              placeholder: "(1-20)"
            },
            {
              label: "|",
              fieldKey: "name",
              required: true,
              placeholder: ""
            },
            {
              label: "",
              fieldKey: "captchaCode",
              required: true,
              placeholder: "",
              renderControl: ({
                value,
                onChange,
                onValidate,
                form,
                error,
                formItemState,
                onUpdateState,
                onSetError,
                setErrors
              }) => (
                <CaptchaInput
                  value={value}
                  error={error}
                  placeholder={""}
                  onChange={onChange}
                  onSetError={onSetError}
                  state={formItemState}
                  onUpdateState={onUpdateState}
                  onValidate={sendCaptcha => {
                    const name = _get(form, "name");
                    if (!name) {
                      setErrors({ name: "" });
                      return;
                    }
                    if (!(isEmail(name) || isPhone(name))) {
                      setErrors({
                        name: ""
                      });
                      return;
                    }
                    const isNameEmail = isEmail(name);
                    const captchaType = isNameEmail
                      ? CAPTCHA_TYPE.EMAIL
                      : CAPTCHA_TYPE.SMS;
                    const actionType = isNameEmail
                      ? ACTION_TYPE.BindEmail
                      : ACTION_TYPE.BindPhoneNumber;
                    onValidate(
                      () =>
                        sendCaptcha(
                          _get(form, "name"),
                          captchaType,
                          actionType
                        ),
                      ["name"]
                    );
                  }}
                />
              )
            },
            {
              label: "",
              fieldKey: "password",
              required: true,
              placeholder: "",
              renderControl: ({ value, onChange }) => (
                <Input
                  type="password"
                  autoComplete="new-password"
                  placeholder={""}
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                  }}
                />
              )
            },
            {
              label: "",
              fieldKey: "confirmPassword",
              required: true,
              placeholder: "",
              renderControl: ({ value, onChange }) => (
                <Input
                  autoComplete="new-password"
                  type="password"
                  placeholder={""}
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                  }}
                />
              )
            }
          ]}
        >
          {({ form, onValidate, onAddErrors }) => {
            return (
              <div
                id={"registerBtn"}
                onClick={() =>
                  onValidate(() => {
                    if (form.confirmPassword !== form.password) {
                      onAddErrors({
                        confirmPassword: ""
                      });
                      return;
                    }

                    const variablesProps = isEmail(form.name || "")
                      ? { email: form.name }
                      : { phone: form.name };

                    registerUser({
                      variables: {
                        username: form.username,
                        password: form.password,
                        verificationCode: form.captchaCode,
                        ...variablesProps
                      }
                    }).then(data => {
                      const result = _get(data, "data.registerUser", {});
                      const { success, message } = result;
                      if (!success) {
                        const splits = _split(message, ":");
                        const errors = { [splits[0]]: splits[1] };
                        onAddErrors(errors);
                      }
                    });
                  })
                }
                style={{ visible: "hidden" }}
              />
            );
          }}
        </BasicForm>
      </Modal.Content>
      <Modal.Actions>
        <Button
          content=""
          labelPosition="right"
          icon="checkmark"
          onClick={() => {
            clickEle("registerBtn");
          }}
          positive
        />
      </Modal.Actions>
    </Modal>
  );
};

export default RegisterModal;
