import React from "react";
import styled from "styled-components";
import ProgramRecommendItem from "../../item/ProgramRecommendItem";
import _get from "lodash/get";
import FlatList from "../../common/FlatList";

const Container = styled.div`
  margin-bottom: 20px;
`;

const ProgramTopGridHRecommends = ({ grid, onGoTo }) => {
  return (
    <Container>
      <FlatList
        items={_get(grid, "bottomRecommends", [])}
        columnsInRow={4}
        gapX={"10px"}
        isFlexItem={true}
        renderItem={({ item }) => {
          return (
            <ProgramRecommendItem
              onGoTo={onGoTo}
              item={item}
              key={Math.random().toString()}
            />
          );
        }}
      />
    </Container>
  );
};

export default ProgramTopGridHRecommends;
