import React from "react";
import styled from "styled-components";

const AdImage = styled.img`
  width: 100%;
`;

const Container = styled.div`
  margin: 10px;
  cursor: pointer;
`;

const AllPageAdBannerItem = ({ item }) => {
  return (
    <Container>
      <AdImage height={140} src={item.url} />
    </Container>
  );
};

export default AllPageAdBannerItem;
