import React from "react";
import styled from "styled-components";
import Avatar from "../common/Avatar";
import _get from "lodash/get";
import { Icon } from "semantic-ui-react";
import { BLOG } from "../../../common/CONSULT";
import { chunkTo } from "../../../utils/textUtils";

const Container = styled.div`
  position: relative;
  display: flex;
  background-color: white;
  cursor: pointer;
  &:hover {
    box-shadow: 0 8px 20px #bbb;
    .blog_title {
      color: #fb6b84;
    }
  }
  flex: 1;
  margin-bottom: 12px;
`;

const ImageBox = styled.div``;

const CoverImage = styled.img``;

const Cover = ({ url }) => {
  return (
    <ImageBox>
      <CoverImage src={url} width={240} height={160} />
    </ImageBox>
  );
};

const FooterBox = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 6px;
`;

const FooterItem = styled.div`
  display: flex;
  align-items: center;
  margin-right: 20px;
`;

const Text = styled.div`
  margin-left: 16px;
`;

const Footer = ({ item }) => {
  return (
    <FooterBox>
      <FooterItem>
        <Avatar url={_get(item, "creator.avatar.url", "")} />
        <Text>{_get(item, "creator.username", "-username-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"calendar alternate outline"} />
        <Text>{_get(item, "dateCreated", "-date-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"heart outline"} />
        <Text>{_get(item, "upCount", "-like-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"eye"} />
        <Text>{_get(item, "readCount", "-read-")}</Text>
      </FooterItem>
    </FooterBox>
  );
};

const ContentBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 10px;
  padding-top: 14px;
  padding-bottom: 8px;
  width: 100%;
`;

const VideoIcon = styled(Icon)``;
const ArticleIcon = styled(Icon)``;

const Title = styled.div`
  font-size: 18px;
  color: #333333;
  font-weight: 700;
  line-height: 22px;
`;

const Description = styled.div`
  flex: 1;
  margin-top: 8px;
  color: #999;
  text-align: left;
  margin-right: 40px;
`;

const ContentHeader = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  padding-right: 20px;
`;
const BlogListItem = ({ item, onGoTo }) => {
  const { id, cover, title, description, type } = item;
  return (
    <Container onClick={() => onGoTo(`${BLOG}${id}`)}>
      <Cover url={_get(cover, "url")} />
      <ContentBox>
        <ContentHeader>
          <Title className={"blog_title"}>{title}</Title>
          {type === "Video" && <VideoIcon name={"video camera"} />}
          {type === "Article" && <ArticleIcon name={"address book outline"} />}
        </ContentHeader>
        <Description>{chunkTo(description, 120)}</Description>
        <Footer item={item} />
      </ContentBox>
    </Container>
  );
};

export default BlogListItem;
