import axios from "axios";
import _get from "lodash/get";

export const getDataFromUrl = (
  url,
  dataPath = "data.content",
  onUpdateValue,
  finalAction
) => {
  axios
    .get(url)
    .then(response => {
      const dataText = _get(response, dataPath, "");
      try {
        onUpdateValue(JSON.parse(dataText) || {});
      } catch (e) {
        onUpdateValue(dataText);
      } finally {
        finalAction && finalAction();
      }
    })
    .catch(error => {
      console.log(error); //
    });
};

export const getJsTextFromUrl = (url, onUpdateValue) => {
  axios
    .get(url)
    .then(response => {
      onUpdateValue(response.data);
    })
    .catch(error => {
      console.log(error); //
    });
};
