import React from "react";
import { useQuery } from "@apollo/react-hooks";
import _last from "lodash/last";
import _spit from "lodash/split";
import _get from "lodash/get";
import { gql } from "apollo-boost";
import { PAGE_SETTINGS_PROPS } from "../common/props";
export const GET_PAGE_SETTINGS = gql`
    query {
        currentWebPageSettings {
            ${PAGE_SETTINGS_PROPS}   
        }
        aboutCodes:codes(available:true,paginateParams:{sort:"codeRank",order:"desc"},types:[ABOUT]){
            count 
            items{
                id
                name
                value
            }
        }
    }
`;

const PageQuery = ({ children }) => {
  const pageSettingsId = _last(_spit(window.location.pathname, "/"));
  const { loading, error, data, refetch, networkStatus } = useQuery(
    GET_PAGE_SETTINGS
  );
  if (!data) {
    return null;
  }
  return children({
    loading,
    refetch,
    pageForm: _get(data, "currentWebPageSettings", {}),
    aboutCodes: _get(data, "aboutCodes.items", [])
  });
};

export default PageQuery;
