import React from "react";
import styled from "styled-components";
import { AppContext } from "../../App";
import _toLower from "lodash/toLower";

const Container = styled.div`
  cursor: pointer;
`;

const getLinkUrl = (objId, objType) => {
  return `/${_toLower(objType)}/${objId}`;
};

const LinkToObject = ({ objId, objType, children }) => {
  return (
    <AppContext.Consumer>
      {props => {
        const { onGoTo } = props;
        return (
          <Container
            onClick={e => {
              e.stopPropagation();
              onGoTo(getLinkUrl(objId, objType));
            }}
          >
            {children}
          </Container>
        );
      }}
    </AppContext.Consumer>
  );
};

export default LinkToObject;
