import React, { useState } from "react";
import styled from "styled-components";
import { Icon } from "semantic-ui-react";

const Container = styled.div`
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 260px;
  height: 80px;
  z-index: 100000;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  transition-duration: 200ms;
  transition-timing-function: linear;
  transition-property: transform;
  transform: scaleX(${props => props.scale}) scaleY(${props => props.scale});
`;

const ToastText = styled.div`
  color: white;
  font-size: 16px;
  margin-left: 6px;
`;

const _ToastHint = ({ onShow, setOnShow }) => {
  const [config, setConfig] = useState({});
  const [type, setType] = useState("");
  const [scale, setScale] = useState(1);
  const [msg, setMsg] = useState("");
  if (!onShow) {
    setOnShow((type, msg, rConfig) => {
      setType(type);
      setMsg(msg);
      setConfig(rConfig);
      // 2s
      const timerScale = setTimeout(() => {
        setScale(1.2);
        clearTimeout(timerScale);
      }, 0);
      // 2s
      const timer = setTimeout(() => {
        setType("");
        setScale(1);
        if (rConfig && rConfig.callback) {
          rConfig.callback();
        }
        clearTimeout(timer);
      }, 2000);
    });
  }
  if (!type) {
    return null;
  }
  return (
    <Container scale={scale}>
      <Icon
        style={{ color: type === "Success" ? "green" : "red" }}
        size={"large"}
        name={
          type === "Success" ? "check circle outline" : "exclamation triangle"
        }
      />
      <ToastText>{msg}</ToastText>
    </Container>
  );
};

class ToastHint {
  static onShow = null;

  static setOnShow = onShowFnc => {
    this.onShow = onShowFnc;
  };
  static hintComponent = (
    <_ToastHint onShow={this.onShow} setOnShow={this.setOnShow} />
  );
  static render() {
    return this.hintComponent;
  }

  static show(type, msg, rConfig) {
    this.onShow(type, msg, rConfig);
  }

  static showSuccess(msg, rConfig) {
    this.show("Success", msg, rConfig);
  }

  static showFailure(msg, rConfig) {
    this.show("Failure", msg, rConfig);
  }
}

export default ToastHint;
