import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { BLOG } from "../../../../common/CONSULT";

const Container = styled.div`
  margin-bottom: 4px;
  cursor: pointer;
  position: relative;
  flex: 1;
  &:hover {
    .blog_name {
      color: #fb6b84;
    }
  }
`;

const CoverImage = styled.img`
  width: 100%;
  height: 90px;
  object-fit: cover;
`;

const Title = styled.div`
  color: white;
  font-size: 14px;
  text-align: left;
`;

const ContentBox = styled.div`
  position: absolute;
  padding: 4px;
  width: 100%;
  z-index: 100;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
`;

const VideoRecommend = ({ item, onGoTo }) => {
  const { id, title, description } = item;
  return (
    <Container
      onClick={() => {
        onGoTo(`${BLOG}${id}`);
      }}
    >
      <CoverImage src={_get(item, "cover.url")} />
      <ContentBox>
        <Title className={"blog_name"}>{title}</Title>
      </ContentBox>
    </Container>
  );
};

export default VideoRecommend;
