import React from "react";
import styled from "styled-components";
import _map from "lodash/map";
import _find from "lodash/find";
import _isEmpty from "lodash/isEmpty";

const Container = styled.div`
  background-color: #f9f9f9;
  padding: 20px 10px;
  min-height: 300px;
  margin-bottom: 30px;
  border-radius: 6px;
`;

const Title = styled.div`
  font-weight: bold;
  font-size: 16px;
  text-align: left;
  margin-bottom: 12px;
`;

const TagsWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const RecommendTags = ({ selectedTagIds, tags, onSelectTag }) => {
  return (
    <Container>
      <Title></Title>
      <TagsWrapper>
        {_map(tags, tag => {
          const selected = _find(selectedTagIds, tagId => tagId === tag.id);
          return (
            <TagItem
              key={"tag_" + tag.id}
              onSelect={onSelectTag}
              tag={tag}
              selected={selected}
            />
          );
        })}
      </TagsWrapper>
    </Container>
  );
};

export default RecommendTags;

const TagItemBox = styled.div`
  height: 30px;
  line-height: 30px;
  border-radius: 15px;
  min-width: 60px;
  padding: 0 10px;
  &:hover {
    border: 1px solid lightskyblue;
    color: dodgerblue;
  }
  border: 1px solid ${props => (props.selected ? "lightblue" : "#cecece")};
  margin: 2px 2px;
  cursor: pointer;
  color: ${props => (props.selected ? "lightblue" : "")};
`;

const TagItem = ({ onSelect, tag, selected }) => {
  return (
    <TagItemBox selected={selected} onClick={() => onSelect(tag)}>
      {tag.name}
    </TagItemBox>
  );
};
