import React from "react";
import styled from "styled-components";
import Slider from "react-slick";
import _map from "lodash/map";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Container = styled.div`
  .slick-dots {
    margin-bottom: 40px;
  }
  .slick-dots li.slick-active button:before {
    color: red;
    font-size: 18px;
  }
  .slick-dots li button:before {
    color: white;
  }
`;

const SwipeImage = styled.img``;

const SwipeItemBox = styled.div`
  cursor: pointer;
  position: relative;
`;

const RecommendContainer = styled.div`
  position: absolute;
  bottom: 40px;
  left: 80px;
`;

const RecommendText = styled.div`
  color: white;
  font-size: 20px;
  font-weight: bold;
`;

const Recommend = ({ label, text }) => {
  return text ? (
    <RecommendContainer>
      <RecommendText>{text}</RecommendText>
    </RecommendContainer>
  ) : null;
};

const isLinkUrl = url => {
  return url.indexOf("http") < 0;
};

const SwipeItem = ({ width, height, item, onGoTo }) => {
  return (
    <SwipeItemBox
      onClick={() =>
        isLinkUrl(item.linkUrl)
          ? onGoTo(item.linkUrl)
          : window.open(item.linkUrl)
      }
    >
      <Recommend text={item.recommendText} label={item.recommendLabel} />
      <SwipeImage width={width} height={height} src={item.url} />
    </SwipeItemBox>
  );
};

const SwipeItems = ({ items, width, height, onGoTo }) => {
  const swipeItemKey = Math.random().toString();
  return (
    <Container style={{ width, height }}>
      <Slider
        dots={true}
        infinite={true}
        speed={500}
        slidesToShow={1}
        slidesToScroll={1}
      >
        {_map(items, item => {
          return (
            <SwipeItem
              key={swipeItemKey + item.index}
              item={item}
              width={width}
              height={height}
              onGoTo={onGoTo}
            />
          );
        })}
      </Slider>
    </Container>
  );
};

export default SwipeItems;
