import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { BLOG } from "../../../../common/CONSULT";
import { chunkTo } from "../../../../utils/textUtils";

const Container = styled.div`
  cursor: pointer;
  display: flex;
  flex: 1;
  margin-bottom: 8px;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 6px;
  border-bottom: 1px dotted
    ${props => (props.isLastOne ? "transparent" : "#dfdfdf")};
  &:hover {
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
    .blog_name {
      color: #fb6b84;
    }
  }
`;

const CoverImage = styled.img`
  height: 80px;
  width: 110px;
  object-fit: cover;
`;

const ContentBox = styled.div`
  margin-left: 4px;
  text-align: left;
`;

const Title = styled.div`
  color: black;
  font-size: 14px;
  font-weight: bold;
  margin-top: 4px;
`;

const Description = styled.div`
  font-size: 12px;
  color: #999;
`;

const ArticleRecommend = ({ item, isLastOne, onGoTo }) => {
  const { id, title, description } = item;
  return (
    <Container onClick={() => onGoTo(`${BLOG}${id}`)} isLastOne={isLastOne}>
      <CoverImage className={"cover_img"} src={_get(item, "cover.url")} />
      <ContentBox>
        <Title className={"blog_name"}>{title}</Title>
        <Description className={"blog_name"}>
          {chunkTo(description, 20)}
        </Description>
      </ContentBox>
    </Container>
  );
};

export default ArticleRecommend;
