import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import BlogListItem from "../item/BlogListItem";
import FlatList from "../common/FlatList";

const Container = styled.div`
  width: 100%;
  padding-right: 12px;
`;

const BlogVListGrid = props => {
  const { grid, onGoTo } = props;
  return (
    <Container>
      <FlatList
        columnsInRow={1}
        isFlexItem={true}
        items={_get(grid, "items", [])}
        renderItem={({ item }) => {
          return (
            <BlogListItem
              item={item}
              onGoTo={onGoTo}
              key={Math.random().toString()}
            />
          );
        }}
      />
    </Container>
  );
};

export default BlogVListGrid;
