import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { BLOG } from "../../../common/CONSULT";

const Container = styled.div`
  flex-wrap: wrap;
  &:hover {
    .blog_name {
      color: #fb6b84;
    }
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
  }
  cursor: pointer;
  margin-bottom: 18px;
`;

const CoverImage = styled.img`
  width: 252px;
  height: 160px;
  margin-left: 4px;
`;

const Title = styled.div`
  color: #333333;
  margin-top: 2px;
  align-items: flex-start;
  text-align: left;
  font-weight: bold;
  margin-left: 4px;
`;

const BlogRecommendListItem = ({ item, onGoTo }) => {
  const { id, title } = item;
  return (
    <Container onClick={() => onGoTo(`${BLOG}${id}`)}>
      <CoverImage className={"cover_img"} src={_get(item, "cover.url")} />
      <Title className={"blog_name"}>{title}</Title>
    </Container>
  );
};

export default BlogRecommendListItem;
