import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import _isEmpty from "lodash/isEmpty";
import _map from "lodash/map";
import { AppContext } from "../../../../App";
import Tags from "../common/Tags";
import AdItem from "../item/AdItem";

const Container = styled.div``;

const PanelBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const PanelTitle = styled.div`
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 8px;
`;

const AdsGridChildrenPanel = ({ title, children }) => {
  return (
    <PanelBox>
      <PanelTitle>{title}</PanelTitle>
      {children}
    </PanelBox>
  );
};

const Content = styled.div``;

const AdsGrid = ({
  tagsKey = "homeTags",
  tagTitle = "",
  grid,
  onGoTo
}) => {
  const items = _get(grid, "items", []);
  return (
    <Container>
      <AppContext.Consumer>
        {({ pageForm }) => {
          const tags = _get(pageForm, tagsKey, []);
          return (
            <Content>
              {!_isEmpty(tags) && (
                <AdsGridChildrenPanel title={tagTitle}>
                  <Tags tags={tags} />
                </AdsGridChildrenPanel>
              )}
              {_map(items, item => {
                return <AdItem item={item} key={item.index} />;
              })}
            </Content>
          );
        }}
      </AppContext.Consumer>
    </Container>
  );
};

export default AdsGrid;
