import { gql } from "apollo-boost";

import {
  BLOG_LIST_PROPS,
  BLOG_PROPS,
  BOOK_LIST_PROPS,
  BOOK_PROPS,
  CHAPTER_PROPS,
  CODE_PROPS,
  COMMENT_ITEM_PROPS,
  COMMENT_PROPS,
  FOLLOWER_LIST_PROPS,
  PROBLEM_LIST_PROPS,
  PROBLEM_PROPS,
  PROGRAM_DATA_PROPS,
  PROGRAM_LIST_PROPS,
  PROGRAM_PROPS,
  PROGRAM_VERSION_LIST_PROPS,
  PROGRAM_VERSION_PROPS,
  TAG_LIST_PROPS,
  TAG_PROPS,
  UP_LIST_PROPS,
  USER_PROPS
} from "./props";

export const GET_PROBLEMS = gql`
    query( $objTypes: [ProblemObjType]
        $statuses: [ProblemStatus]
        $type:ProblemType
        $creatorId:ID
        $dateCreated: [Date]
        $lastUpdated: [Date]
        $limit: Int
        $offset: Int
        $sort:String
        $order:String
    ) {
        problems( paginateParams: {
            max: $limit
            offset: $offset
            sort: $sort
            order: $order
        }
            objTypes: $objTypes
            type: $type
            creatorId:$creatorId
            statuses: $statuses
            dateCreated: $dateCreated
            lastUpdated: $lastUpdated) {
            count
            items{
                ${PROBLEM_LIST_PROPS}
            }

        }
    }
`;

export const GET_PROBLEM = gql`
    query ($id:ID!){
        problem(id:$id){
            ${PROBLEM_PROPS}
        }
    }
`;

export const GET_PROGRAM = gql`
query ($id:ID!){
    program(id:$id){
        ${PROGRAM_PROPS}
    }
}`;

export const GET_PROGRAM_WITH_VERSION = gql`
    query ($id:ID!,$version:ID!){
        program(id:$id){
            ${PROGRAM_PROPS}
        }
        programVersion(id:$version){
            ${PROGRAM_VERSION_PROPS}
        }
    }
`;

export const GET_PROGRAMS = gql`
    query( $name: String
        $code:String
        $creatorId:ID 
        $statuses: [ProgramStatus]
        $dateCreated: [Date]
        $lastUpdated: [Date]
        $limit: Int
        $offset: Int
        $sort:String
        $order:String
    ) {
        programs( paginateParams: {
            max: $limit
            offset: $offset
            sort: $sort
            order: $order
             }
            name: $name
            code:$code
            creatorId:$creatorId
            searchType:Program
            statuses: $statuses
            dateCreated: $dateCreated
            lastUpdated: $lastUpdated) {
            count
            items{
                ${PROGRAM_LIST_PROPS}
            }

        }
    }
`;

