export const SEARCH_ITEMS = gql`
  query searchItems(
    $searchKeyword: String
    $searchType: SearchType
    $tagIds: [ID!]!
    $limit: Int
    $offset: Int
    $sort:String
    $order:String
  ) {  
    books( paginateParams: {
             max: $limit
             offset: $offset
             sort: $sort
             order: $order
           }, 
             name: $searchKeyword, searchType: $searchType,tagIds: $tagIds,statuses:[Published]){
        count
        items{
            ${BOOK_LIST_PROPS}
        }
    }
    blogs(
        paginateParams: {
            max: $limit
            offset: $offset
            sort: $sort
            order: $order
        },
        title: $searchKeyword,
        tagIds: $tagIds
        searchType: $searchType,
        inCollection:false,
      statuses:[Published]
        )
       {
        count
        items{ 
            ${BLOG_LIST_PROPS}
        }
    }
    problems(
        paginateParams: {
            max: $limit
            offset: $offset
            sort: $sort
            order: $order
        },
        title: $searchKeyword, searchType: $searchType,tagIds: $tagIds,statuses:[Published]){
        count
        items{
            ${PROBLEM_LIST_PROPS}
        }
    }
   programs(
       paginateParams: {
           max: $limit
           offset: $offset
           sort: $sort
           order: $order
       },
       name: $searchKeyword, searchType: $searchType,tagIds: $tagIds,statuses:[Published]){
          count
          items{ 
              ${PROGRAM_LIST_PROPS}
          }
   }
  }
`;

export const GET_SEARCH_TAGS = gql`
  query($searchKeyword: String){
      tags(paginateParams:{max:30,offset:0},name: $searchKeyword){
          count
          items{
              ${TAG_LIST_PROPS}
          }
      }
  }
`;

export const GET_COMMENTS = gql`
  query ( 
      $objId:String
      $types:[CommentType]
      $parentId:ID
      $searchParentComment:Boolean
      $isUserQuery:Boolean
      $isUserRelated:Boolean
      $creatorId:ID
      $limit: Int
      $offset: Int
      $sort:String
      $order:String){
      comments(
        paginateParams: {
          max: $limit
          offset: $offset
          sort: $sort
          order: $order
          } 
          objId:$objId
          types:$types
          searchParentComment:$searchParentComment
          isUserQuery:$isUserQuery
          isUserRelated:$isUserRelated
          creatorId:$creatorId
          parentId:$parentId
      ){
          count
          items{
              ${COMMENT_ITEM_PROPS}
          }
      }
  }
`;

