export const get_codes = ITEM_PROPS => gql`
  query(
    $name: String
    $value: String
    $rank:[Int]
    $description:String
    $types: [CodeType]
    $dateCreated: [Date]
    $lastUpdated: [Date]
    $limit: Int
    $offset: Int
    $sort:String
    $order:String
  ) {
    codes(
      paginateParams: {
        max: $limit
        offset: $offset
        sort: $sort
        order: $order
      }
      name: $name
      value:$value
      rank:$rank
      types:$types
      available:true
      description:$description
      dateCreated: $dateCreated
      lastUpdated: $lastUpdated
    ) {
      items {
        ${ITEM_PROPS ? ITEM_PROPS : CODE_PROPS}
      }
      count
    }
  }
`;

export const GET_REPORT_ITEMS_GROUPS = get_codes(`
     id
     name
     value
     reportItems{
        id
        name
     }
`);

export const GET_GIT_TYPES = get_codes(`
     id
     name
     value
`);

