import React from "react";
import styled from "styled-components";
import _isEmpty from "lodash/isEmpty";
import _map from "lodash/map";
import Tag from "./Tag";

const Container = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
`;

const Title = styled.div`
  text-align: left;
  font-size: 18px;
  font-weight: bold;
`;

const Header = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 60px;
`;

const TagsBox = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex: 1;
`;

const Tags = ({ tags, onSelectTag }) => {
  if (_isEmpty(tags)) {
    return null;
  }
  return (
    <Container>
      <Header>
        <Title></Title>
      </Header>

      <TagsBox>
        {_map(tags, tag => {
          return <Tag onClose={() => onSelectTag(tag)} tag={tag} />;
        })}
      </TagsBox>
    </Container>
  );
};

export default Tags;
