import React, { useState } from "react";
import styled from "styled-components";
import PersistStorage from "../../../utils/PersistStorage";
import _isEmpty from "lodash/isEmpty";
import _map from "lodash/map";
import _filter from "lodash/filter";
import _chunk from "lodash/chunk";
import { Icon } from "semantic-ui-react";
import { onSaveSearchHistory } from "../../header/Header";
import { SEARCH } from "../../../common/CONSULT";

const Container = styled.div`
  background-color: #f9f9f9;
  padding: 20px 10px;
  min-height: 300px;
  margin-bottom: 30px;
  border-radius: 6px;
`;

const Title = styled.div`
  font-weight: bold;
  font-size: 16px;
  text-align: left;
  margin-bottom: 12px;
`;

const HistoryPanel = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const SearchItemBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30px;
  min-width: 40px;
  text-wrap: none;
  padding: 2px 4px;
  border-bottom: 1px solid #cecece;
  cursor: pointer;
  margin: 2px 4px;
  .remove_btn {
    display: none;
  }
  &:hover {
    color: dodgerblue;
    border-bottom: 1px solid lightskyblue;
    .remove_btn {
      display: block;
    }
  }
`;

const ItemText = styled.div`
  font-size: 14px;
  max-width: 100px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
`;

const CloseIcon = styled(Icon)`
  margin-bottom: 4px !important;
  color: black;
  &:hover {
    color: #fb6b84;
  }
`;

const onRemoveSearchItem = searchItem => {
  const historySearchItems = PersistStorage.getItem("historySearchItems");
  const newHistorySearchItems = _filter(
    historySearchItems,
    item => item !== searchItem
  );
  PersistStorage.setItem("historySearchItems", newHistorySearchItems);
};

const SearchItem = ({ item, onRefresh, onSearch }) => {
  return (
    <SearchItemBox>
      <ItemText onClick={() => onSearch(item)}>{item}</ItemText>
      <CloseIcon
        className={"remove_btn"}
        name={"close"}
        onClick={() => {
          onRemoveSearchItem(item);
          onRefresh();
        }}
      />
    </SearchItemBox>
  );
};

const SearchHistory = ({ onGoTo }) => {
  const historySearchItems = PersistStorage.getItem("historySearchItems");
  const [refresh, setRefresh] = useState("refresh");
  const onSearch = keyword => {
    onSaveSearchHistory(keyword);
    onGoTo(`${SEARCH}${keyword}`);
  };
  if (_isEmpty(historySearchItems)) {
    return null;
  }
  return (
    <Container>
      <Title></Title>
      <HistoryPanel key={refresh}>
        {_map(_chunk(historySearchItems, 20)[0], (item, index) => {
          return (
            <SearchItem
              key={"search_item_" + index}
              onRefresh={() => setRefresh(Math.random().toString())}
              item={item}
              onSearch={onSearch}
            />
          );
        })}
      </HistoryPanel>
    </Container>
  );
};

export default SearchHistory;
