import { PERSONAL, PERSONAL_PAGES } from "../common/CONSULT";
import _values from "lodash/values";

export const currentPath = () => {
  return window.location.pathname;
};

export const refreshCurrentPage = (isLogout = false) => {
  const path = currentPath();
  if (isLogout && path.indexOf("/personal/") >= 0) {
    document.location.assign("/");
  } else {
    document.location.assign(path);
  }
};
export const isHomePath = () => {
  const path = currentPath();
  if (path === "/" || !path) {
    return true;
  }
  return false;
};

export const getPageByPath = () => {
  if (isHomePath()) {
    return "home";
  }
  const path = currentPath();
  switch (path) {
    case "/blog":
      return "blog";
    case "/program":
      return "program";
    case "/book":
      return "book";
    case "/problem":
      return "problem";
    case "/about":
      return "about";
  }
};

export const getPersonPage = () => {
  const path = currentPath();
  let page = PERSONAL_PAGES.USER_SETTINGS;
  for (const value of _values(PERSONAL_PAGES)) {
    if (path.indexOf(PERSONAL + value) >= 0) {
      return value;
    }
  }
  return page;
};

export const getPathByPage = page => {
  switch (page) {
    case "home":
      return "";
    default:
      return "/" + page;
  }
};
