import { gql } from "apollo-boost";

import {
  COMMENT_ITEM_PROPS,
  PROBLEM_PROPS,
  PROGRAM_DATA_PROPS,
  PROGRAM_VERSION_LIST_PROPS,
  PROGRAM_VERSION_PROPS_DETAIL,
  REPORT_PROPS,
  USER_PROPS
} from "./props";

export const SAVE_PROBLEM = gql`
    mutation ( $id: ID
        $type: ProblemType!
        $title: String!
        $description:String!
    ) {
        saveProblem( 
           id: $id 
           type: $type
           title: $title
           description: $description
        ) {
           ${PROBLEM_PROPS}

        }
    }
`;

export const SWITCH_UP = gql`
  mutation($objId: ID!, $objType: ObjType!) {
    switchUp(objId: $objId, objType: $objType)
  }
`;
export const SWITCH_FOLLOW = gql`
  mutation($objId: ID!, $objType: ObjType!) {
    switchFollower(objId: $objId, objType: $objType)
  }
`;

export const SAVE_COMMENT = gql`
  mutation(
    $id: ID
    $type: CommentType!
    $objId: String!
    $parentId: ID
    $repliedCommentId: ID
    $content: String!
  ) {
    saveComment(
      id: $id
      type: $type
      objId: $objId
      parentId: $parentId
      repliedCommentId: $repliedCommentId
      content: $content
    ) {
      id
    }
  }
`;

export const SAVE_COMMENT_FOR_DETAILS = gql`
    mutation(
        $id: ID
        $type: CommentType!
        $objId: String!
        $parentId: ID
        $repliedCommentId: ID
        $content: String!
    ) {
        saveComment(
            id: $id
            type: $type
            objId: $objId
            parentId: $parentId
            repliedCommentId: $repliedCommentId
            content: $content
        ) {
            ${COMMENT_ITEM_PROPS}
        }
    }
`;

export const SWITCH_UP_COMMENT = gql`
  mutation($commentId: ID!) {
    switchUpComment(commentId: $commentId) {
      id
      upCount
      currentUpped
    }
  }
`;

export const SAVE_REPORT = gql`
  mutation ($id:ID,$objId:String!,$type:ReportType!,$reportReason:String,$itemIds:[ID!]!){
      saveReport(id:$id,objId:$objId,type:$type,reportReason:$reportReason,itemIds:$itemIds){
          ${REPORT_PROPS}
      }
  }
`;

export const SAVE_RATING = gql`
  mutation($objId: ID!, $type: RatingType!, $score: Int!) {
    saveRating(objId: $objId, type: $type, score: $score) {
      id
      averageScore
      ratingCount
      score
    }
  }
`;

export const SAVE_CURRENT_USER = gql`
   mutation ($username:String!,$nickname:String,$fullName:String,$email:String,$phone:String,$avatarUrl:String){
       saveCurrentUser(username:$username,nickname:$nickname,fullName:$fullName,email:$email,phone:$phone,avatarUrl:$avatarUrl){
           ${USER_PROPS}
       }
   }
`;

export const UPDATE_PROGRAM_STATUS = gql`
  mutation($id: ID!, $status: ProgramStatus!) {
    updateProgramStatus(id: $id, status: $status) {
      success
    }
  }
`;

export const SAVE_PROGRAM = gql`
  mutation(
    $id: ID
    $name: String!
    $code: String
    $coverUrl: String
    $data: String
    $description: String
    $tags: [String!]
    $gitInputs: [GitInput!]
    $helpVideo: String
    $helpDescription: String
  ) {
    saveProgram(
      id: $id
      name: $name
      code: $code
      coverUrl: $coverUrl
      data: $data
      description: $description
      tags: $tags
      gitInputs: $gitInputs
      helpVideo: $helpVideo
      helpDescription: $helpDescription
    ) {
      id
    }
  }
`;

export const GET_PROGRAM_VERSION = gql`
  query($id: ID!) {
    programVersion(id: $id) {
      ${PROGRAM_VERSION_PROPS_DETAIL}
    }
  }
`;

export const SAVE_PROGRAM_VERSION = gql`
  mutation(
    $id: ID
    $name: String
    $programId: ID!
    $importType: ImportType
    $codeUrl: String
    $versionCode: String!
    $description: String
  ) {
    saveProgramVersion(
      id: $id
      name: $name
      programId: $programId
      importType: $importType
      codeUrl: $codeUrl
      versionCode: $versionCode
      description: $description
    ) {
      id
    }
  }
`;

export const UPDATE_PROGRAM_VERSION_STATUS = gql`
  mutation($id: ID!, $status: ProgramVersionStatus!) {
    updateProgramVersionStatus(id: $id, status: $status) {
      id
    }
  }
`;

export const UNBIND_PHONE = gql`
  mutation {
    unbindPhone {
      success
      message
    }
  }
`;

export const UNBIND_EMAIL = gql`
  mutation {
    unbindEmail {
      success
      message
    }
  }
`;

export const READ_OBJ = gql`
  mutation($objId: ID!, $objType: ObjType!) {
    updateObjReadCount(objId: $objId, objType: $objType) {
      success
      message
    }
  }
`;

export const SAVE_PROGRAM_DATA = gql`
  mutation ($programId:ID!,$type:String,$isLarge:Boolean,$isOpened:Boolean,$data:String,$uniqueId:String){
      saveProgramData(programId:$programId,type:$type,isLarge:$isLarge,isOpened:$isOpened,data:$data,uniqueId:$uniqueId){
          ${PROGRAM_DATA_PROPS}
      }
  }
`;
