import React from "react";
import styled from "styled-components";
import { Rating } from "semantic-ui-react";
import { PROGRAM } from "../../../common/CONSULT";

const Container = styled.div`
  margin-left: 4px;
  margin-right: 4px;
  margin-bottom: 16px;
  cursor: pointer;
  &:hover {
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
    .program_name {
      color: #fb6b84;
    }
    .ui.rating .active.icon {
      color: goldenrod !important;
    }
  }
`;

const CoverImage = styled.img`
  width: 196px;
  height: 170px;
  object-fit: cover;
`;

const BottomBox = styled.div`
  margin-top: 4px;
`;

const Name = styled.div`
  font-size: 16px;
  font-weight: 700;
  color: black;
  width: 100%;
  text-align: left;
`;

const RateBox = styled.div`
  display: flex;
  align-items: center;
  margin-top: 6px;

  .ui.rating .active.icon {
    color: grey !important;
  }
`;

const RateNum = styled.div`
  margin-left: 10px;
  font-size: 10px;
  color: grey;
`;

const ProgramListItem = ({ item, onGoTo }) => {
  const {
    id,
    name,
    cover: { url },
    averageScore,
    scoreCount
  } = item;
  const hasScoreCount = scoreCount && scoreCount > 0.1;
  return (
    <Container
      onClick={() => {
        onGoTo(`${PROGRAM}${id}`);
      }}
    >
      <CoverImage className={"cover_img"} src={url} />
      <BottomBox>
        <Name className={"program_name"}>{name}</Name>
        <RateBox>
          <Rating maxRating={5} disabled rating={averageScore} />
          {hasScoreCount ? <RateNum>{scoreCount}</RateNum> : null}
        </RateBox>
      </BottomBox>
    </Container>
  );
};

export default ProgramListItem;
