import React from "react";
import styled from "styled-components";
import BookRecommendItem from "../../item/BookRecommendItem";
import FlatList from "../../common/FlatList";
import _get from "lodash/get";

const Container = styled.div`
  width: 340px;
  margin-left: 6px;
`;

const BookTopGridRecommends = ({ grid, onGoTo }) => {
  return (
    <Container>
      <FlatList
        items={_get(grid, "recommends", [])}
        columnsInRow={2}
        gap={"4px"}
        isFlexItem={true}
        renderItem={({ item }) => {
          return (
            <BookRecommendItem
              onGoTo={onGoTo}
              key={Math.random()}
              item={item}
            />
          );
        }}
      />
    </Container>
  );
};

export default BookTopGridRecommends;
