import React, { useEffect, useState } from "react";
import { Modal, Header, Icon, Button } from "semantic-ui-react";

const ErrorModal = ({
  children,
  open,
  message,
  errorTitle = "",
  onHide
}) => {
  return (
    <Modal
      style={{ top: 200 }}
      centered={false}
      closeIcon
      open={open}
      onClose={onHide}
    >
      <Header icon="info circle" content={errorTitle} />
      <Modal.Content>
        <p>{message}</p>
      </Modal.Content>
      <Modal.Actions>
        <Button color="red" onClick={onHide}>
          <Icon name="remove" /> 
        </Button>
      </Modal.Actions>
    </Modal>
  );
};

export default ErrorModal;
