import React from "react";
import styled from "styled-components";
import { PROGRAM } from "../../../common/CONSULT";

const Container = styled.div`
  cursor: pointer;
  position: relative;
  flex: 1;
  &:hover {
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
    .program_name {
      color: #333333;
      font-size: 18px;
    }
    .name_box {
      background: transparent;
    }
  }
`;

const CoverImage = styled.img`
  height: 200px;
  width: 100%;
`;

const Name = styled.div`
  color: white;
  font-weight: bold;
  font-size: 16px;
  width: 100%;
  text-align: center;
  z-index: 100;
`;

const NameBox = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 4px 12px 10px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
`;

const ProgramRecommendItem = ({ item, onGoTo }) => {
  const {
    cover: { url },
    name,
    id,
    recommendText
  } = item;
  return (
    <Container onClick={() => onGoTo(`${PROGRAM}${id}`)}>
      <CoverImage className={"cover_img"} src={url} />
      <NameBox className={"name_box"}>
        <Name className={"program_name"}>{recommendText || name}</Name>
      </NameBox>
    </Container>
  );
};

export default ProgramRecommendItem;
