import React, { useState, useEffect } from "react";
import styled from "styled-components";
import "braft-editor/dist/index.css";
import BraftEditor from "braft-editor";
import { getDataFromUrl } from "../utils/urlData";

const useEditor = ({ value }) => {
  const [defaultValue, setDefaultValue] = useState(null);
  const [refreshKey, setRefresh] = useState("refresh");
  const onUpdateDefaultValue = content => {
    setDefaultValue(BraftEditor.createEditorState(content));
    setRefresh(Math.random.toString());
  };
  useEffect(() => {
    getDataFromUrl(value, "data.content", onUpdateDefaultValue);
  }, [defaultValue === null]);

  return { defaultValue, refreshKey };
};

const RichTextEditor = ({
  placeholder,
  disabled,
  language = "zh",
  fieldKey,
  value,
  onUpdateField
}) => {
  const { defaultValue, refreshKey } = useEditor({ value });
  return (
    <BraftEditor
      key={refreshKey}
      onChange={editorState => onUpdateField(fieldKey, editorState.toHTML())}
      placeholder={placeholder}
      className="my-editor"
      language={language}
      readOnly={disabled}
      defaultValue={defaultValue}
    />
  );
};

export default RichTextEditor;
