import React, { useState, useEffect, lazy, Suspense } from "react";
import styled from "styled-components";
import Header from "./header/Header";
import { getDataFromUrl } from "../utils/urlData";
import PageQuery from "./PageQuery";
import HomePage from "./home/HomePage";
import BlogPage from "./blog/BlogPage";
import ProgramPage from "./program/ProgramPage";
import BookPage from "./book/BookPage";
import _get from "lodash/get";
import _set from "lodash/get";
import _find from "lodash/find";
import _clone from "lodash/clone";
import _each from "lodash/each";
import _keysIn from "lodash/keysIn";
import _pick from "lodash/pick";
import _isArray from "lodash/isArray";
import _merge from "lodash/merge";
import { AppContext } from "../../App";
import Routes from "./Routes";
import { withRouter } from "react-router-dom";
import { getPageByPath, getPathByPage } from "../utils/pathUtils";
import ProblemPage from "./problem/ProblemPage";
import ProbEditPage from "./problem/ProblemEditPage";
import Loading from "../common/Loading";
import {
  BLOG,
  BOOK,
  CHAPTER,
  PROBLEM_CREATE,
  PROBLEM_EDIT,
  PROBLEM_PAGE,
  PROGRAM,
  SEARCH,
  PERSONAL,
  ABOUT
} from "../common/CONSULT";
import ModalHint from "../common/ModalHint";
import SearchPage from "./search/SearchPage";
import ReportHint from "../common/ReportHint";
import LoginHint from "../common/LoginHint";
import ToastHint from "../common/ToastHint";
import { useMe } from "../common/hooks";
import BackToTop from "../common/BackToTop";
import Footer from "../common/Footer";

const ProblemEditPage = lazy(() => import("./problem/ProblemEditPage"));
const ProblemCreatePage = lazy(() => import("./problem/ProblemCreatePage"));
const Program = lazy(() => import("./program/Program"));
const Blog = lazy(() => import("./blog/Blog"));
const Book = lazy(() => import("./book/Book"));
const Chapter = lazy(() => import("./book/Chapter"));
const PersonalPage = lazy(() => import("./personalCenter/PersonalPage"));
const Problem = lazy(() => import("./problem/Problem"));
const AboutPage = lazy(() => import("./about/AboutPage"));

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: ${document.body.clientHeight}px;
  background-color: ${props => props.bgColor};
`;

const PageBox = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const PAGES = ["home", "program", "blog", "book"];

const iteratePageItems = (pageData, handleItem) => {
  const pages = _get(pageData, "pages");

  const pagesConfig = _pick(pages, PAGES);

  _each(pagesConfig, (page, currentPage) => {
    const rows = _get(page, "rows", []);
    _each(rows, row => {
      const grids = _get(row, "grids", []);
      _each(grids, grid => {
        _each(_keysIn(grid), objKey => {
          const gValue = grid[objKey];
          if (_isArray(gValue)) {
            _each(gValue, (v, index) => {
              handleItem(v, currentPage, row.index, grid.index, objKey, index);
            });
          } else if (gValue instanceof Object && gValue[0]) {
            _each(gValue, (v, index) => {
              handleItem(v, currentPage, row.index, grid.index, objKey, index);
            });
          }
          // else if (gValue instanceof Object) {
          //   handleItem(
          //     gValue,
          //     currentPage,
          //     row.index,
          //     grid.index,
          //     objKey,
          //     null
          //   );
          // }
        });
      });
    });
  });
};

const mergeQueryItems = (pData, blogs, books, programs) => {
  const pageData = _clone(pData);
  iteratePageItems(
    pData,
    (item, page, rowIndex, gridIndex, objKey, itemIndex) => {
      const typeName = _get(item, "__typename");
      let queryItem = {};
      switch (typeName) {
        case "Blog":
          queryItem = _find(blogs, blog => blog.id === item.id);
          break;
        case "Book":
          queryItem = _find(books, book => book.id === item.id);
          break;
        case "Program":
          queryItem = _find(programs, program => program.id === item.id);
          break;
      }
      const newItem = _merge(item, queryItem);
      const objPath = `pages.${page}.rows[${rowIndex}].grids[${gridIndex}].${objKey}`;
      if (itemIndex >= 0) {
        const path = objPath + `[${itemIndex}]`;

        _set(pageData, path, newItem);
      } else {
        _set(pageData, objPath, newItem);
      }
    }
  );
  return pageData;
};

const useAppPage = ({ pageForm, history }) => {
  const { dataUrl, books, programs, blogs } = pageForm;
  const [pageData, setPageData] = useState({});

  console.log("pageForm______________________________AA", pageData);
  useEffect(() => {
    getDataFromUrl(dataUrl, "data.content", data => {
      setPageData(mergeQueryItems(data, blogs, books, programs));
    });
  }, []);

  const onSwitchPage = page => {
    history.push(getPathByPage(page));
  };

  const onGoTo = url => {
    history.push(url);
  };

  return {
    page: getPageByPath(),
    onSwitchPage,
    pageData,
    onGoTo
  };
};

const _AppPage = ({
  pageForm,
  aboutCodes,
  history,
  location,
  currentUser,
  setAuthentication,
  openedObj,
  bgColor
}) => {
  const { page, onSwitchPage, pageData, onGoTo } = useAppPage({
    pageForm,
    history
  });
  const { head, pages } = pageData;
  const commonProps = { onGoTo, history, location, currentUser };
  return (
    <Container bgColor={bgColor}>
      <Header
        currentPage={page}
        onSwitchPage={onSwitchPage}
        data={head || {}}
        onGoTo={onGoTo}
        openedObj={openedObj}
      />
      <PageBox>
        <Routes
          routes={[
            {
              path: `${SEARCH}:searchKeyword`,
              component: <SearchPage {...commonProps} />
            },
            {
              path: `${SEARCH}`,
              component: <SearchPage {...commonProps} />
            },
            {
              path: BLOG,
              component: (
                <BlogPage {...commonProps} page={_get(pages, "blog")} />
              )
            },
            {
              path: `${BLOG}:id`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <Blog {...commonProps} />
                </Suspense>
              )
            },
            {
              path: PROGRAM,
              exact: true,
              component: (
                <ProgramPage {...commonProps} page={_get(pages, "program")} />
              )
            },
            {
              path: BOOK,
              exact: true,
              component: (
                <BookPage {...commonProps} page={_get(pages, "book")} />
              )
            },
            {
              path: `${BOOK}:id`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <Book {...commonProps} />
                </Suspense>
              )
            },
            {
              path: `${CHAPTER}:id`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <Chapter {...commonProps} />{" "}
                </Suspense>
              )
            },
            {
              path: PROBLEM_CREATE,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <ProblemCreatePage {...commonProps} />
                </Suspense>
              )
            },
            {
              path: `${PROBLEM_PAGE}:id`,
              component: (
                <Suspense fallback={<Loading />}>
                  <Problem {...commonProps} />
                </Suspense>
              )
            },
            {
              path: `${PROBLEM_EDIT}/:id`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <ProblemEditPage {...commonProps} />
                </Suspense>
              )
            },
            {
              path: PROBLEM_PAGE,
              exact: true,
              component: <ProblemPage {...commonProps} />
            },
            {
              path: `${PROGRAM}:id`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <Program {...commonProps} />
                </Suspense>
              )
            },
            {
              path: `${PROGRAM}:id/:version`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <Program {...commonProps} />
                </Suspense>
              )
            },
            {
              path: `${PERSONAL}:page`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <PersonalPage {...commonProps} />
                </Suspense>
              )
            },
            {
              path: `${ABOUT}`,
              exact: true,
              component: (
                <Suspense fallback={<Loading />}>
                  <AboutPage {...commonProps} aboutCodes={aboutCodes} />
                </Suspense>
              )
            },
            {
              path: "",
              component: (
                <HomePage {...commonProps} page={_get(pages, "home")} />
              )
            }
          ]}
        />
      </PageBox>
      <Footer />
      {ModalHint.render()}
      {ReportHint.render()}
      {LoginHint.render(setAuthentication, currentUser)}
      {ToastHint.render()}
      <BackToTop />
    </Container>
  );
};

const AppPage = props => {
  const { me } = useMe();
  const [openedObj, setOpenedObj] = useState(null);
  const { currentUser, setAuthentication, logout, history } = props;
  const [bgColor, setGlobalBgColor] = useState("white");
  return (
    <PageQuery>
      {({ refetch, pageForm, aboutCodes, loading }) => {
        return (
          <AppContext.Provider
            value={{
              pageForm,
              openedObj,
              setOpenedObj,
              currentUser: { ...me, ...currentUser },
              setAuthentication,
              logout,
              bgColor,
              setGlobalBgColor,
              onGoTo: url => history.push(url)
            }}
          >
            <_AppPage
              refetch={refetch}
              bgColor={bgColor}
              loading={loading}
              pageForm={pageForm}
              aboutCodes={aboutCodes}
              openedObj={openedObj}
              {...props}
              currentUser={{ ...currentUser, ...me }}
            />
          </AppContext.Provider>
        );
      }}
    </PageQuery>
  );
};

export default withRouter(AppPage);
