import _find from "lodash/find";

export const getOptionByKey = (options, key, value) => {
  if (options instanceof Array) {
    return _find(options, o => o[key] === value);
  }
  return options[key];
};

export const getValueByKey = (options, { valueKey = "value", key, value }) => {
  if (options instanceof Array) {
    const option = getOptionByKey(options, key, value);
    return option[valueKey];
  }
  return getOptionByKey(options, key, value);
};
