import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { _getByRank } from "../../../utils/utils";
import { BOOK } from "../../../common/CONSULT";

const Container = styled.div`
  padding: 0 8px;
  width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin-bottom: 12px;
  &:hover {
    .book_name {
      color: #fb6b84;
    }
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
  }
`;

const CoverImage = styled.img``;

const Name = styled.div`
  font-size: 16px;
  font-weight: bold;
  color: black;
  margin-top: 6px;
`;

const Author = styled.div`
  margin-top: 4px;
  color: #cecece;
`;

const BookHListItem = ({ item, onGoTo }) => {
  return (
    <Container onClick={() => onGoTo(`${BOOK}${item.id}`)}>
      <CoverImage
        className={"cover_img"}
        width={172}
        height={200}
        src={_get(item, "cover.url")}
      />
      <Name className={"book_name"}>{_get(item, "name")}</Name>
      <Author>
        {_getByRank(item, ["creator.nickname", "creator.username"])}/ 
      </Author>
    </Container>
  );
};

export default BookHListItem;
