import React, { useEffect } from "react";
import styled from "styled-components";
import { Search } from "semantic-ui-react";
import PersistStorage from "../../utils/PersistStorage";
import _map from "lodash/map";
import _get from "lodash/get";
import _chunk from "lodash/chunk";
import { getSearchKeyword } from "../../utils/routeUtils";

const SearchInput = ({ isLoading, onSearch }) => {
  useEffect(() => {
    const handleKeypress = event => {
      if (event.keyCode === 13) {
        onSearch(event.target.value);
      }
    };
    const searchInput = document.getElementById("search_input");
    searchInput.addEventListener("keypress", handleKeypress);
    return () => searchInput.removeEventListener("keypress", handleKeypress);
  }, []);
  const displayHistorySearchItems = _map(
    _chunk(PersistStorage.getItem("historySearchItems"), 10)[0],
    i => ({ title: i })
  );
  return (
    <Search
      id={"search_input"}
      input={{ icon: "search", iconPosition: "left" }}
      loading={isLoading}
      onResultSelect={(e, data) => {
        const value = _get(data, "result.title");
        onSearch(value);
      }}
      className={"search_header"}
      style={{ width: 400 }}
      placeholder={""}
      // onSearchChange={e => {
      // }}
      results={displayHistorySearchItems}
      defaultValue={getSearchKeyword()}
      // onSearchChange={_.debounce(this.handleSearchChange, 500, {
      //     leading: true,
      // })}
      // results={results}
      // value={value}
    />
  );
};

export default SearchInput;
