import isEmail from "validator/lib/isEmail";

import isPhone from "validator/lib/isMobilePhone";
import _each from "lodash/each";
import _get from "lodash/get";
import _reverse from "lodash/reverse";
import _keys from "lodash/keys";
import _isEmpty from "lodash/isEmpty";
import _isInteger from "lodash/isInteger";

// rules = {"required":{value:"",message:""}}

export const validateForm = (form, items) => {
  console.log("form___________________AAA", form);
  let errors = {};
  _each(items, item => {
    const { rules = {}, fieldKey, required, label } = item;
    const value = _get(form, fieldKey);
    if (required) {
      if (_isEmpty(value)) {
        errors[fieldKey] = rules["required"] || `${label}`;
      }
    }
    _each(_reverse(rules), (rule, key) => {
      const strValue = value ? value.toString() : "";
      switch (key) {
        case "maxLength":
          if (strValue.length > (rule.value || 0)) {
            errors[fieldKey] = rule.message || `${rule.value}`;
          }
          break;
        case "minLength":
          if (strValue.length < (rule.value || 0)) {
            errors[fieldKey] = rule.message || `${rule.value}`;
          }
          break;
        case "isEmail":
          if (value && !isEmail(value)) {
            errors[fieldKey] = rule.message || "";
          }
          break;
        case "phone":
          if (!isPhone(value)) {
            errors[fieldKey] = rule.message || "";
          }
          break;
        case "isCaptcha":
          if (value && value.length !== 6) {
            errors[fieldKey] = rule.message || "6";
          }
        case "isTelPhone":
          break;
      }
    });
  });

  const noError = _isEmpty(_keys(errors));
  return noError ? null : errors;
};
