import React from "react";
import styled from "styled-components";
import SwipeItems from "../common/SwipeItems";
import _get from "lodash/get";
import BookTopGridRecommends from "./book/BookTopGridRecommends";

const Container = styled.div`
  display: flex;
`;

const BookTopGrid = ({ grid,onGoTo }) => {
  return (
    <Container>
      <SwipeItems onGoTo={onGoTo} width={960} height={400} items={_get(grid, "items", [])} />
      <BookTopGridRecommends onGoTo={onGoTo} grid={grid} />
    </Container>
  );
};

export default BookTopGrid;
