import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import ProgramItem from "../item/ProgramItem";
import FlatList from "../common/FlatList";
const Container = styled.div`
  margin-bottom: 10px;
`;

const ProgramFlatListGrid = ({ grid, onGoTo }) => {
  const items = _get(grid, "items", []);
  return (
    <Container>
      <FlatList
        items={items}
        itemWidth={300}
        renderItem={({ item }) => (
          <ProgramItem item={item} key={item.index} onGoTo={onGoTo} />
        )}
      />
    </Container>
  );
};

export default ProgramFlatListGrid;
