import React from "react";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  border-bottom: 1px solid #f2f5f6;
`;

const TabsBox = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 500px;
`;

const Tabs = ({ currentTab, onSwitchTab }) => {
  const commonProps = { currentTab, onSwitchTab };
  return (
    <Container>
      <TabsBox>
        <Tab {...commonProps} title={""} tab={"All"} />
        <Tab {...commonProps} title={""} tab={"Program"} />
        <Tab {...commonProps} title={""} tab={"Blog"} />
        <Tab {...commonProps} title={""} tab={"Book"} />
        {/*<Tab {...commonProps} title={""} tab={"Problem"} />*/}
      </TabsBox>
    </Container>
  );
};

export default Tabs;

const TabBox = styled.div`
  padding: 10px 2px;
  border-bottom: 2px solid
    ${props => (props.selected ? "#E83939" : "transparent")};
`;

const TabText = styled.div`
  font-size: ${props => (props.selected ? 24 : 24)}px;
  font-weight: bold;
  color: ${props => (props.selected ? "#E83939" : "")};
  cursor: pointer;
`;

const Tab = ({ tab, title, currentTab, onSwitchTab }) => {
  const selected = tab === currentTab;
  return (
    <TabBox selected={selected} onClick={() => onSwitchTab(tab)}>
      <TabText selected={selected}>{title}</TabText>
    </TabBox>
  );
};
