import React from "react";
import styled from "styled-components";
import BlogRecommendListItem from "../item/BlogRecommendListItem";
import FlatList from "../common/FlatList";
import _get from "lodash/get";

const Container = styled.div``;

const BlogRecommendListGrid = ({ grid, onGoTo }) => {
  return (
    <Container>
      <FlatList
        items={_get(grid, "items", [])}
        columnsInRow={5}
        itemWidth={252}
        renderItem={({ item }) => {
          return (
            <BlogRecommendListItem
              key={Math.random().toString()}
              onGoTo={onGoTo}
              item={item}
            />
          );
        }}
      />
    </Container>
  );
};

export default BlogRecommendListGrid;
