import { useEffect, useState } from "react";
import { SERVER_URL } from "../config";
import ApolloClient from "apollo-boost";
import axios from "axios";
import PersistStorage from "../components/utils/PersistStorage";
import { refreshCurrentPage } from "../components/utils/pathUtils";

const getAuthentication = async () => {
  console.log("try to get authentication");
  const userInfo = PersistStorage.getItem("authentication");
  console.log("userInfo.................", userInfo);
  const expiredTime = PersistStorage.getItem("expiredTime");
  console.log("expiredTime................", expiredTime);
  if (
    !userInfo ||
    !expiredTime ||
    parseInt(expiredTime) <= new Date().getTime()
  ) {
    return null;
  }
  try {
    return userInfo;
  } catch (e) {
    console.log("error parsing cached authentication, reauthenticate");
  }
};

const setAuthentication = async authUser => {
  PersistStorage.setItem("authentication", authUser);

  PersistStorage.setItem(
    "expiredTime",
    (new Date().getTime() + authUser.expires_in * 1000).toString()
  );
  PersistStorage.setItem("username", authUser.username);

  await initialize();
};

const refreshToken = async () => {
  const { SERVER_URL } = this.props.appConfig;
  console.log("props_____", this.props);
  console.log("try to refreshToken authentication");
  const authString = PersistStorage.getItem("authentication");
  console.log("refreshToken.................", authString);
  const expiredTime = PersistStorage.getItem("expiredTime");
  let refreshToken = null;
  if (authString) {
    refreshToken = JSON.parse(authString).refresh_token;
  } else {
    return null;
  }
  console.log("refreshToken.................", refreshToken);
  if (
    refreshToken ||
    (expiredTime && parseInt(expiredTime) <= new Date().getTime())
  ) {
    await axios
      .post(SERVER_URL + "/oauth/access_token", {
        grant_type: "refresh_token",
        refresh_token: refreshToken
      })
      .then(async response => {
        const beforeData = JSON.parse(authString);
        console.log("beforeDatabeforeData", beforeData);
        const userInfo = response.data;
        userInfo["username"] = beforeData.username;
        userInfo["roles"] = beforeData.roles;
        await setAuthentication(userInfo);
        // document.location.assign("/user");
      })
      .catch(error => {
        alert("login error");
      });
  }
};

const initialize = async () => {
  const userInfo = await getAuthentication();
  const initParams = {
    uri: SERVER_URL + "/graphql",
    credentials: "include"
  };

  if (userInfo) {
    initParams.headers = { Authorization: "Bearer " + userInfo.access_token };
  }

  initParams.onError = error => {
    if (error.networkError && error.networkError.statusCode === 401) {
      refreshToken();
    }
  };
  const client = new ApolloClient(initParams);
  return { client, userInfo };
  // if (currentUser && window.location.pathname === "/") {
  //   document.location.assign("/");
  // }
};

const logout = async () => {
  PersistStorage.removeItem("authentication");
  refreshCurrentPage(true);
  // document.location.assign("/");
};

const useApp = () => {
  const [apolloClient, setApolloClient] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);
  useEffect(() => {
    initialize().then(value => {
      const { client, userInfo } = value;
      setApolloClient(client);
      setCurrentUser(userInfo);
    });
  }, []);

  return { apolloClient, currentUser, setAuthentication, logout };
};

export default useApp;
