import React, { useReducer, useEffect } from "react";
import styled from "styled-components";
import { useQuery } from "@apollo/react-hooks";
import { gql } from "apollo-boost";
import { PROBLEM_LIST_PROPS } from "../../common/props";
import SearchFilterFields from "./components/SearchFilterFields";
import PaginationQueryList from "../../common/PaginationQueryList";
import _map from "lodash/map";
import _uniq from "lodash/uniq";
import ProblemItem from "../../common/item/ProblemItem";
import SearchEmpty from "../search/segaments/SearchEmpty";
import { OBJ_TYPES, SEARCHED_PROBLEMS } from "../../common/CONSULT";
import PersistStorage from "../../utils/PersistStorage";
import LinkToObject from "../../common/LinkToObject";
import PageTitle from "../../common/PageTitle";

export const GET_PROBLEMS = gql` 
  query( 
    $objTypes: [ProblemObjType]
    $statuses: [ProblemStatus]
    $type:ProblemType
    $creatorId:ID
    $dateCreated: [Date]
    $lastUpdated: [Date] 
    $limit: Int
    $offset: Int
    $sort:String
    $order:String
 ) {
    problems( paginateParams: {
      max: $limit
      offset: $offset
      sort: $sort
      order: $order
    }
      objTypes: $objTypes
      type: $type 
      creatorId:$creatorId
      statuses: $statuses
      dateCreated: $dateCreated
      lastUpdated: $lastUpdated) {
        count
        items{
            ${PROBLEM_LIST_PROPS}
        }
      
    }
  }
`;

const Container = styled.div`
  width: 1300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 40px;
  margin-top: 18px;
`;

const Problems = ({ items }) => {
  useEffect(() => {
    PersistStorage.setItem(SEARCHED_PROBLEMS, items);
  }, [items]);
  return (
    <>
      {_map(items, item => {
        return (
          <LinkToObject
            key={"problem_" + item.id}
            objId={item.id}
            objType={OBJ_TYPES.PROBLEM}
          >
            <ProblemItem problem={item} />
          </LinkToObject>
        );
      })}
    </>
  );
};

const initSearchFields = { statuses: ["Published", "Accepted"] };

const useProblemPage = ({ currentUser }) => {
  const [state, dispatch] = useReducer(
    (state, action) => {
      let searchFields = initSearchFields;
      const selectedTag = state.selectedTag;
      if (selectedTag === action.type) {
        return { searchFields, selectedTag: "" };
      }
      switch (action.type) {
        case "resolved":
          searchFields = { ...searchFields, statuses: ["Accepted"] };
          break;
        case "unsolved":
          searchFields = { ...searchFields, statuses: ["Published"] };
          break;
        case "bug":
          searchFields = { ...searchFields, type: "Bug" };
          break;
        case "advice":
          searchFields = { ...searchFields, type: "Advice" };
          break;
        case "my":
          searchFields = {
            ...searchFields,
            creatorId: currentUser.id,
            statuses: []
          };
          break;
      }
      return { searchFields, selectedTag: action.type };
    },
    {
      searchFields: initSearchFields,
      selectedTag: ""
    }
  );
  return {
    state,
    onSwitchTag: tag => dispatch({ type: tag })
  };
};

const ProblemPage = ({ currentUser }) => {
  const { state, onSwitchTag } = useProblemPage({ currentUser });
  const { searchFields, selectedTag } = state;
  return (
    <Container>
      <PageTitle title={""} />
      <SearchFilterFields onSwitchTag={onSwitchTag} selectedTag={selectedTag} />
      <PaginationQueryList
        searchFields={searchFields}
        hidePaginationInSinglePage
        gql={GET_PROBLEMS}
        dataKey={"problems"}
        limit={10}
      >
        {({ loading, count, items, refetch }) => {
          if (count <= 0) {
            return <SearchEmpty text={""} />;
          }
          return <Problems items={items} />;
        }}
      </PaginationQueryList>
    </Container>
  );
};

export default ProblemPage;
