import React from "react";
import { GRIDS } from "./CONSULT";
import HomeSwipeGrid from "../grids/HomeSwipeGrid";
import BlogListGrid from "../grids/BlogListGrid";
import BookFlatListGrid from "../grids/BookFlatListGrid";
import BookListGrid from "../grids/BookListGrid";
import AdsGrid from "../grids/AdsGrid";
import ProgramFlatListGrid from "../grids/ProgramFlatListGrid";
import ProgramTopGrid from "../grids/ProgramTopGrid";
import ProgramListGrid from "../grids/ProgramListGrid";
import BookTopGrid from "../grids/BookTopGrid";
import BookHListGrid from "../grids/BookHListGrid";
import BlogRecommendListGrid from "../grids/BlogRecommendListGrid";
import AllPageAdBanner from "../grids/AllPageAdBanner";
import BlogTopGrid from "../grids/BlogTopGrid";
import BlogVListGrid from "../grids/BlogVListGrid";
import AnnouncementBannerGrid from "../grids/AnnouncementBannerGrid";

export const getGridById = props => {
  const {
    grid: { id }
  } = props;
  switch (id) {
    case GRIDS.homeSwipe.id:
      return <HomeSwipeGrid {...props} />;
    case GRIDS.blogList.id:
      return <BlogListGrid {...props} />;
    case GRIDS.bookFlatList.id:
      return <BookFlatListGrid {...props} />;
    case GRIDS.bookList.id:
      return <BookListGrid {...props} />;
    case GRIDS.adList.id:
      return <AdsGrid {...props} />;
    case GRIDS.programFlatList.id:
      return <ProgramFlatListGrid {...props} />;
    case GRIDS.announcement.id:
      return <AnnouncementBannerGrid {...props} />;

    case GRIDS.programTop.id:
      return <ProgramTopGrid {...props} />;
    case GRIDS.programList.id:
      return <ProgramListGrid {...props} />;

    case GRIDS.bookTop.id:
      return <BookTopGrid {...props} />;

    case GRIDS.bookHList.id:
      return <BookHListGrid {...props} />;

    case GRIDS.blogRecommendList.id:
      return <BlogRecommendListGrid {...props} />;

    case GRIDS.allPageBannerAd.id:
      return <AllPageAdBanner {...props} />;

    case GRIDS.blogTop.id:
      return <BlogTopGrid {...props} />;
    case GRIDS.blogVList.id:
      return <BlogVListGrid {...props} />;

    default:
      return <div>Grid</div>;
  }
};
