import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { Icon } from "semantic-ui-react";
import { BLOG } from "../../../../common/CONSULT";
import Avatar from "../../../transformable/common/Avatar";

const Container = styled.div`
  position: relative;
  display: flex;
  cursor: pointer;
  &:hover {
    box-shadow: 0px 7px 7px -7px #bbb;
    border-bottom: 1px solid transparent;
    .blog_name {
      color: #fb6b84;
    }
    .blog_description {
      font-size: 14px;
    }
  }
  margin-bottom: 18px;
  padding-bottom: 10px;
  border-bottom: 1px solid ${props => (props.isLastOne ? "none" : "#cecece")};
`;

const ImageBox = styled.div`
  margin-right: 10px;
`;

const CoverImage = styled.img``;

const Cover = ({ url }) => {
  return (
    <ImageBox>
      <CoverImage src={url} width={240} height={160} />
    </ImageBox>
  );
};

const FooterBox = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 6px;
`;

const FooterItem = styled.div`
  display: flex;
  align-items: center;
  margin-right: 20px;
`;

const Text = styled.div`
  margin-left: 16px;
`;

const Footer = ({ item }) => {
  return (
    <FooterBox>
      <FooterItem>
        <Avatar url={_get(item, "creator.avatar.url", "")} />
        <Text>{_get(item, "creator.username", "-username-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"calendar alternate outline"} />
        <Text>{_get(item, "dateCreated", "-date-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"heart outline"} />
        <Text>{_get(item, "upCount", "-like-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"eye"} />
        <Text>{_get(item, "readCount", "-read-")}</Text>
      </FooterItem>
    </FooterBox>
  );
};

const ContentBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 6px;
  color: #999;
`;

const Title = styled.div`
  font-size: 18px;
  color: #333333;
  font-weight: 600;
  margin-top: 8px;
`;

const Description = styled.div`
  flex: 1;
  margin-top: 14px;
  font-size: 13px;
  line-height: 24px;
  margin-bottom: 16px;
`;

const BlogItem = ({ item, onGoTo, isLastOne }) => {
  const { id, cover, title, description } = item;
  const imgUrl = _get(cover, "url");
  return (
    <Container isLastOne={isLastOne} onClick={() => onGoTo(`${BLOG}${id}`)}>
      {imgUrl && <Cover url={imgUrl} />}
      <ContentBox>
        <Title className={"blog_name"}>{title}</Title>
        <Description className={"blog_description"}>{description}</Description>
        <Footer item={item} />
      </ContentBox>
    </Container>
  );
};

export default BlogItem;
