import React, { useState } from "react";
import styled from "styled-components";
import BasicForm from "../../common/BasicForm";
import { Input, Button, Select } from "semantic-ui-react";
import RichTextEditor from "../../common/RichTextEditor";
import { useMutation } from "@apollo/react-hooks";
import { SAVE_PROBLEM } from "../../common/mutationGql";
import _get from "lodash/get";
import { PROBLEM_EDIT } from "../../common/CONSULT";
import { useCopyUrlsToFields } from "../../common/hooks";
import PageTitle from "../../common/PageTitle";

const Container = styled.div``;

const PROBLEM_OPTIONS = [
  { text: "", value: "Advice" },
  { text: "Bug", value: "Bug" }
];

const ProblemForm = ({
  formData = { type: "Advice" },
  loading,
  history,
  refetch,
  isEdit
}) => {
  const { item: form } = useCopyUrlsToFields(formData, "descriptionUrl");
  const [saveProblem, { data, loading: mLoading }] = useMutation(SAVE_PROBLEM, {
    onCompleted: result => {
      const id = _get(result, "saveProblem.id");
      if (!formData) {
        history.push(`${PROBLEM_EDIT}/${id}`);
      } else {
        history.push(`${PROBLEM_EDIT}/${id}`);
      }
    }
  });

  return (
    <>
      <PageTitle
        title={`${isEdit ? "" : ""}-${form.title}`}
        metaKeywords={`${form.title}`}
      />
      <BasicForm
        key={form.description}
        loading={loading || mLoading}
        width={900}
        formData={form}
        onValue={() => {}}
        items={[
          {
            label: "",
            required: true,
            fieldKey: "title",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <Input
                value={value}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },
          {
            label: "",
            required: true,
            fieldKey: "type",
            renderControl: ({ value, onChange }) => (
              <Select
                disabled={isEdit}
                options={PROBLEM_OPTIONS}
                defaultValue={value}
                onChange={(e, { name, value }) => {
                  console.log("value____GGDSFSDFDS______", value);
                  onChange(value);
                }}
                placeholder={``}
              />
            )
          },
          {
            label: "",
            fieldKey: "description",
            required: true,
            placeholder: ``,
            renderControl: ({
              item: { placeholder, fieldKey },
              value,
              onUpdateField
            }) => {
              return (
                <RichTextEditor
                  placeholder={placeholder}
                  fieldKey={fieldKey}
                  value={value}
                  onUpdateField={onUpdateField}
                />
              );
            }
          }
        ]}
      >
        {({ form, onValidate }) => {
          return (
            <Button
              style={{ width: 200 }}
              onClick={() => {
                onValidate(() =>
                  saveProblem({
                    variables: {
                      id: form.id,
                      title: form.title,
                      type: form.type,
                      description: form.description
                    }
                  })
                );
              }}
              color="teal"
            >
              
            </Button>
          );
        }}
      </BasicForm>
    </>
  );
};

export default ProblemForm;
