export const useMockDataUrl = (itemData, ...fieldKeys) => {
  const dataUrls = _pick(itemData, fieldKeys);
  const [item, setItem] = useState(itemData);
  useEffect(() => {
    _each(dataUrls, (dataUrl, fieldKeyUrl) => {
      getDataFromUrl(dataUrl, "data.content", data => {
        const fieldKey = fieldKeyUrl.slice(0, fieldKeyUrl.length - 3);
        setItem({ ...item, [fieldKey]: data });
      });
    });
  }, []);

  return { item };
};

export const useCopyUrlsToFields = (itemData, ...fieldKeys) => {
  const [item, setItem] = useState(itemData);
  useEffect(() => {
    _each(fieldKeys, fieldKeyUrl => {
      const fieldKey = fieldKeyUrl.slice(0, fieldKeyUrl.length - 3);
      setItem({ ...item, [fieldKey]: itemData && itemData[fieldKeyUrl] });
    });
  }, []);
  return { item };
};

export const useComment = ({
  refetch,
  commentType,
  objId,
  parentId,
  repliedCommentId,
  clearForm,
  saveCallback
}) => {
  const [show, setShow] = useState(false);
  const [saveComment, { data, loading }] = useMutation(
    saveCallback ? SAVE_COMMENT_FOR_DETAILS : SAVE_COMMENT,
    {
      onCompleted: data => {
        refetch && refetch();
        clearForm && clearForm();
        saveCallback && saveCallback(_get(data, "saveComment"));
      },
      onError: () => ModalHint.error("")
    }
  );
  const onSaveComment = content => {
    if (_isEmpty(content)) {
      ModalHint.error("");
    } else {
      saveComment({
        variables: {
          type: commentType,
          objId,
          parentId,
          repliedCommentId,
          content
        }
      });
    }
  };

  return { show, setShow, mLoading: loading, onSaveComment };
};

export const useReplyForm = ({ repliedComment }) => {
  const { creator } = repliedComment;
  const [show, setShow] = useState(false);
  return {
    showReplyForm: show,
    setShowReplyForm: setShow,
    replyPlaceholder: ` @${_getByRank(creator, ["nickname", "username"])}:`
  };
};

export const useRefresh = () => {
  const [refresh, setRefresh] = useState("refresh");
  const onRefresh = () => setRefresh(Math.random().toString());
  return [refresh, onRefresh];
};

export const useSwitchFollow = ({
  currentFollowed,
  objId,
  objType,
  onSwitchCompleted,
  followCount: fCount = 0,
  successMsg = { followed: "", unFollowed: "" },
  errorMsg = { followed: "", unFollowed: "" }
}) => {
  const [followed, setFollowed] = useState(currentFollowed);
  const [followCount, setFollowCount] = useState(fCount);
  const [switchFollow, { data, loading }] = useMutation(SWITCH_FOLLOW, {
    onCompleted: data => {
      onSwitchCompleted && onSwitchCompleted(data);
      // 
      PersistStorage.setItem(`${objType}${objId}${UPDATED}`, true);
      ToastHint.showSuccess(
        !followed ? successMsg.followed : successMsg.unFollowed
      );
      setFollowed(!followed);
      setFollowCount(data.switchFollower);
    },
    onError: e => {
      console.log(e);
      ModalHint.error(!followed ? errorMsg.followed : errorMsg.unFollowed);
    }
  });
  return {
    followed,
    setFollowed,
    mLoading: loading,
    onSwitchFollow: () =>
      LoginHint.checkLogin(() =>
        switchFollow({ variables: { objId, objType } })
      ),
    followCount
  };
};

export const useSwitchUp = ({
  currentUpped,
  objId,
  objType,
  onSwitchCompleted,
  upCount: uCount = 0,
  successMsg = { upped: "", unUpped: "" },
  errorMsg = { upped: "", unUpped: "" }
}) => {
  const [upped, setUpped] = useState(currentUpped);
  const [upCount, setUpCount] = useState(uCount);
  const [switchUp, { data, loading }] = useMutation(SWITCH_UP, {
    onCompleted: data => {
      onSwitchCompleted && onSwitchCompleted(data);
      ToastHint.showSuccess(!upped ? successMsg.upped : successMsg.unUpped);
      setUpped(!upped);
      setUpCount(data.switchUp);
    },
    onError: () => ModalHint.error(!upped ? errorMsg.upped : errorMsg.unUpped)
  });
  return {
    upped,
    setUpped,
    mLoading: loading,
    onSwitchUp: () =>
      LoginHint.checkLogin(() => switchUp({ variables: { objId, objType } })),
    upCount
  };
};

export const useRating = ({
  currentRating,
  objId,
  objType,
  onRateCompleted,
  successMsg = "",
  errorMsg = ""
}) => {
  const [rating, setRating] = useState(
    currentRating && currentRating.isCurrentRating
      ? currentRating
      : { ...currentRating, score: 0 }
  );
  const [rate, { data, loading }] = useMutation(SAVE_RATING, {
    onCompleted: data => {
      const score = data.saveRating.score;
      onRateCompleted && onRateCompleted(data);
      ToastHint.showSuccess(successMsg + `${score}`);
      setRating(data.saveRating);
    },
    onError: error => {
      console.error(error);
      //ModalHint.error(errorMsg);
    }
  });
  return {
    rating,
    setRating,
    mLoading: loading,
    onRate: score =>
      LoginHint.checkLogin(() =>
        rate({ variables: { objId, type: objType, score } })
      )
  };
};

export const useMe = () => {
  const { loading, error, data, refetch } = useQuery(GET_ME);
  const me = _get(data, "me", null);
  return { me, loading, refetch };
};

export const useTabs = ({ initTab }) => {
  const [currentTab, setCurrentTab] = useState(initTab);
  const [countState, dispatchCount] = useReducer((state, action) => {
    if (action.tab && action.count) {
      return { ...state, [action.tab]: action.count };
    }
    return state;
  }, {});
  const onUpdateTabCount = (tab, count) => dispatchCount({ tab, count });
  return {
    currentTab,
    onSwitchTab: tab => setCurrentTab(tab),
    onUpdateTabCount,
    countState
  };
};

// program
export const useUpdateProgramStatus = ({
  programId,
  onUpdateStatusCompleted
}) => {
  const [status, setStatus] = useState("");
  const [successMsg, setSuccessMsg] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const [updateProgramStatus, { data, loading }] = useMutation(
    UPDATE_PROGRAM_STATUS,
    {
      onCompleted: data => {
        onUpdateStatusCompleted && onUpdateStatusCompleted(status);
        ToastHint.showSuccess(successMsg);
      },
      onError: () => ModalHint.error(errorMsg)
    }
  );

  return {
    mLoading: loading,
    onUpdateProgramStatus: (status, successMsg, errorMsg) => {
      setSuccessMsg(successMsg);
      setErrorMsg(errorMsg);
      setStatus(status);
      updateProgramStatus({ variables: { id: programId, status } });
    }
  };
};

// file upload

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener("load", () => callback(reader.result));
  reader.readAsDataURL(img);
}

export const useFileInput = ({ isTmp, value, onChange }) => {
  const [loading, setLoading] = useState(false);
  const [imageUrl, setImageUrl] = useState(value);
  const handleChange = info => {
    if (info.file.status === "uploading") {
      setLoading(true);
    }
    if (info.file.status === "done") {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl => {
        setLoading(false);
        setImageUrl(imageUrl);
        const ossImageUrl = _get(info.file, "response.pathArray[0]");
        onChange(ossImageUrl);
      });
    }
  };
  return {
    loading,
    imageUrl,
    handleChange,
    actionUrl: SERVER_URL + (isTmp ? "/uploadTemp" : "/uploadm")
  };
};

const callCaptcha = (
  name,
  captchaType,
  actionType,
  onSuccess,
  onFail,
  onFinal
) => {
  if (!name) {
    ToastHint.showFailure("");
    return;
  }
  const postData =
    captchaType === CAPTCHA_TYPE.SMS ? { phone: name } : { email: name };
  axios
    .post(`${SERVER_URL}/${_toLower(captchaType)}Captcha`, {
      actionType,
      ...postData
    })
    .then(response => {
      onSuccess && onSuccess(response.data);
      console.log("response______", response);
    })
    .catch(error => {
      onFail && onFail(error);
      console.log("call captcha error", error);
    })
    .finally(() => {
      onFinal && onFinal();
    });
};

export const useSendCaptcha = ({
  captchaType,
  actionType,
  onUpdateState,
  onCompleted,
  count,
  onError
}) => {
  const onSendCaptcha = (name, cType, aType) => {
    callCaptcha(
      name,
      cType || captchaType,
      aType || actionType,
      res => {
        onCompleted && onCompleted(res);
        onUpdateState("loading", true);
      },
      onError,
      () => {
        onUpdateState("loading", true);
        onUpdateState("count", count + 1);
      }
    );
  };
  return { onSendCaptcha };
};

