export const useRead = ({ objId, objType }) => {
  const [readObj, { data, loading: mLoading }] = useMutation(READ_OBJ, {
    onCompleted: () => {},
    onError: () => {}
  });
  useEffect(() => {
    const lastReadDate = PersistStorage.getItem(
      `readCount[${objType}][${objId}]`
    );
    const currentDate = new Date().getTime();
    const readNow = lastReadDate
      ? currentDate - lastReadDate > 1000 * 60 * 30
      : true;
    // 30min
    if (readNow) {
      if (objId && objType) {
        readObj({ variables: { objId, objType } });
      }
      PersistStorage.setItem(`readCount[${objType}][${objId}]`, currentDate);
    }
  }, []);
};

export const useIframeLoading = (eleId = "program_iframe") => {
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const programIframe = document.getElementById(eleId);
    programIframe.onload = () => {
      setLoading(false);
    };
  }, []);
  return { loading };
};

export const useResizeIframe = () => {
  const [width, setWidth] = useState("100%");
  const [height, setHeight] = useState(400);
  useEffect(() => {
    const messageHandler = event => {
      const {
        data: { height: programHeight, width: programWidth }
      } = event;
      setHeight(programHeight + 4);
      const currentWidth = document.getElementById("program_iframe")
        .clientWidth;
      if (currentWidth && programWidth > currentWidth) {
        setWidth(programWidth);
      }
    };
    window.addEventListener("message", messageHandler);
    return () => window.removeEventListener("message", messageHandler);
  }, []);
  return { width, height };
};

export const useRefetch = (refetch, deps, cond, clearRefetch) => {
  useEffect(() => {
    if (cond) {
      refetch && refetch();
      clearRefetch && clearRefetch();
    }
  }, deps || []);
};

export const useRefetchItem = (refetch, refetchKey, objId) => {
  const objRefresh = PersistStorage.getItemRO(refetchKey);
  const refreshValue = _get(objRefresh, objId);
  useRefetch(refetch, [], refreshValue === REFETCH_TAG, () => {
    PersistStorage.setItem(refetchKey, { ...objRefresh, [objId]: "" });
  });
};
