import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import VideoRecommend from "./VideoRecommend";
import FlatList from "../../common/FlatList";
const Container = styled.div``;

const BlogTopGridVideoBlogRecommends = ({
  grid,
  itemsKey = "blogRecommends",
  onGoTo
}) => {
  const items = _get(grid, itemsKey, []);
  return (
    <Container>
      <FlatList
        items={items}
        isFlexItem={true}
        columnsInRow={1}
        gapY={"10px"}
        renderItem={({ item }) => {
          return (
            <VideoRecommend
              onGoTo={onGoTo}
              item={item}
              key={Math.random().toString()}
            />
          );
        }}
      />
    </Container>
  );
};

export default BlogTopGridVideoBlogRecommends;
