import React from "react";
import styled from "styled-components";
import SearchContent from "./SearchContent";
import ProgramItem from "./items/ProgramItem";
import FlatList from "../../../common/FlatList";

const Container = styled.div`
  margin-right: 40px;
`;

const ProgramsBox = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
`;

const ProgramPanel = ({
  currentTab,
  pagination,
  programs,
  onUpdatePagination,
  onGoTo,
  onSearchMore,
  isAll
}) => {
  const { count, items } = programs;
  if (isAll && count <= 0) {
    return null;
  }
  return (
    <Container>
      <SearchContent
        currentTab={currentTab}
        pagination={pagination}
        itemsCount={count}
        title={""}
        emptyText={""}
        tab={"Program"}
        onSearchMore={onSearchMore}
        onUpdatePagination={onUpdatePagination}
      >
        <ProgramsBox>
          <FlatList
            items={items}
            columns={3}
            itemWidth={"310px"}
            rowGap={"10px"}
          >
            {({ item: program, isLastRow }) => {
              return (
                <ProgramItem
                  key={"program___" + program.id}
                  item={program}
                  onGoTo={onGoTo}
                />
              );
            }}
          </FlatList>
        </ProgramsBox>
      </SearchContent>
    </Container>
  );
};

export default ProgramPanel;
