import React from "react";
import styled from "styled-components";
import _map from "lodash/map";
import SearchInput from "./SearchInput";
import LoginAvatar from "./LoginAvatar";
import { SEARCH } from "../../common/CONSULT";
import PersistStorage from "../../utils/PersistStorage";
import _filter from "lodash/filter";
import { getSearchKeyword } from "../../utils/routeUtils";
import OpenedObj from "./OpenedObj";
import { Link } from "react-router-dom";

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  width: 100%;
  background: #fff;
  position: relative;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 8%);
  .search_header {
    //position: absolute !important;
    //left: 0 !important;
    //right: 0 !important;
    //margin: auto;
    .prompt {
      width: 400px;
      height: 40px;
    }
  }
`;

const LeftBox = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
`;

const RightBox = styled.div`
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
  padding-left: 40px;
  flex: 1;
`;

const HEAD_TABS = [
  { name: "", value: "home" },
  { name: "", value: "program" },
  { name: "", value: "blog" },
  { name: "", value: "book" },
  { name: "", value: "problem" },
  // { name: "", value: "announcement" },
  { name: "", value: "about" }
];

const HeadTab = styled.div`
  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;
  font-size: ${props => (props.selected ? "24" : "20")}px;
  color: ${props => (props.selected ? "#EC808D" : "#333333")};
  text-align: center;
  line-height: ${props => (props.selected ? "24" : "20")} px;
  margin-bottom: ${props => (props.selected ? 30 : 0)} px;
`;

const HeadTabsContainer = styled.div`
  display: flex;
`;

const HeadTabs = ({ onSwitchTab, selectedTab }) => {
  return (
    <HeadTabsContainer>
      {_map(HEAD_TABS, (tab, index) => {
        const { name, value } = tab;
        return (
          <HeadTab
            key={index}
            selected={selectedTab === value}
            onClick={() => onSwitchTab(value)}
          >
            {name}
          </HeadTab>
        );
      })}
    </HeadTabsContainer>
  );
};

const LogoContainer = styled.div``;

const LogoImage = styled.img`
  width: 180px;
  height: 70px;
`;

const LogoField = ({ logo }) => {
  return (
    <LogoContainer>
      <Link to={"/"}>
        <LogoContainer lineHeight={logo ? 0 : 70}>
          {logo ? <LogoImage src={logo} /> : "LOGO"}
        </LogoContainer>
      </Link>
    </LogoContainer>
  );
};

export const onSaveSearchHistory = keyword => {
  const historySearchItems = PersistStorage.getItem("historySearchItems") || [];
  const newHistorySearchItems = _filter(
    historySearchItems,
    item => item !== keyword
  );
  newHistorySearchItems.unshift(keyword);
  PersistStorage.setItem(
    "historySearchItems",
    newHistorySearchItems.filter(i => i)
  );
};

const Header = ({
  data: { logo },
  currentPage,
  onSwitchPage,
  onGoTo,
  openedObj
}) => {
  return (
    <Container>
      <LeftBox>
        <LogoField logo={logo} />
        <HeadTabs selectedTab={currentPage} onSwitchTab={onSwitchPage} />
      </LeftBox>
      <SearchInput
        key={getSearchKeyword()}
        onSearch={searchKeyword => {
          onSaveSearchHistory(searchKeyword);
          onGoTo(`${SEARCH}${searchKeyword}`);
        }}
      />
      <RightBox>
        <OpenedObj openedObj={openedObj} />
        <LoginAvatar onGoTo={onGoTo} />
      </RightBox>
    </Container>
  );
};

export default Header;
