import React from "react";
import styled from "styled-components";
import _map from "lodash/map";
import _get from "lodash/get";
import BlogListItem from "../item/BlogListItem";

const Container = styled.div`
  margin-right: 20px;
  background-color: whitesmoke;
`;

const BlogListGrid = ({ grid, onGoTo }) => {
  const items = _get(grid, "items", []);
  const randKey = Math.random().toString();
  return (
    <Container>
      {_map(items, item => {
        return (
          <BlogListItem
            item={item}
            key={randKey + item.index}
            onGoTo={onGoTo}
          />
        );
      })}
    </Container>
  );
};

export default BlogListGrid;
