import React from "react";
import styled from "styled-components";

const AdImage = styled.img`
  object-fit: contain;
  width: 100%;
`;

const Container = styled.div`
  width: 100%;
  &:hover {
  }
  margin: 10px;
`;

const AdItem = ({ item }) => {
  return (
    <Container>
      <AdImage src={item.url} />
    </Container>
  );
};

export default AdItem;
