import React from "react";
import styled from "styled-components";
import { Button, Icon } from "semantic-ui-react";
import _map from "lodash/map";

const FILTER_ITEMS = [
  { label: "", value: "resolved" },
  { label: "", value: "unsolved" },
  { label: "BUG", value: "bug" },
  { label: "", value: "advice" },
  { label: "", value: "my" }
];

const Container = styled.div``;

const FilterButton = ({ item, selected, onSwitchTag }) => {
  const { label, value } = item;
  return (
    <Button
      onClick={() => onSwitchTag(item.value)}
      basic
      icon
      color={selected ? "grey" : "blue"}
      style={{ marginLeft: 30, width: 120 }}
    >
      {selected && <Icon style={{ marginRight: 20 }} name="check" />}
      {label}
    </Button>
  );
};

const FilterButtons = ({ selectedTag, onSwitchTag }) => {
  return (
    <Container>
      {_map(FILTER_ITEMS, (item, index) => {
        return (
          <FilterButton
            onSwitchTag={onSwitchTag}
            selected={item.value === selectedTag}
            item={item}
            key={"program_filter_button" + index}
          />
        );
      })}
    </Container>
  );
};

export default FilterButtons;
