import React, { useState, useEffect, useReducer } from "react";
import styled from "styled-components";
import Tabs from "./segaments/Tabs";
import SearchContent from "./segaments/SearchContent";
import { getRouteId, getSearchKeyword } from "../../utils/routeUtils";
import { GET_SEARCH_TAGS, SEARCH_ITEMS } from "../../common/queryGql";
import _map from "lodash/map";
import PageItem from "../QueryItem";
import LoadingWrapper from "../../common/LoadingWapper";
import SearchResultPanel from "./segaments/SearchResultPanel";
import _findIndex from "lodash/findIndex";
import _filter from "lodash/filter";
import _get from "lodash/get";

const Container = styled.div`
  width: 1300px;
  margin-top: 18px;
`;

const useSearchPage = ({ location }) => {
  const [currentTab, setCurrentTab] = useState("All");

  return {
    currentTab,
    setCurrentTab,
    selectedTag: _get(location, "state.selectedTag", null),
    searchKeyword: getSearchKeyword()
  };
};

const SearchBox = styled.div``;

const _SearchPageResult = ({
  loading,
  searchResult,
  tagIds,
  tags,
  onSelectTag,
  searchTags,
  searchType,
  searchKeyword,
  onUpdatePagination,
  currentTab,
  pagination,
  onSearchMore,
  onGoTo
}) => {
  return (
    <SearchBox>
      <SearchResultPanel
        pagination={pagination}
        onUpdatePagination={onUpdatePagination}
        searchResult={searchResult}
        tagIds={tagIds}
        tags={tags}
        onGoTo={onGoTo}
        searchTags={searchTags}
        onSelectTag={onSelectTag}
        currentTab={currentTab}
        onSearchMore={onSearchMore}
      />
    </SearchBox>
  );
};

const initPagination = {
  offset: 0,
  limit: 8
};

const tabMax = {
  All: 6,
  Program: 12,
  Blog: 8,
  Book: 8
};

const getPagination = currentTab => {
  return {
    offset: 0,
    limit: tabMax[currentTab]
  };
};

const useSearchResult = ({ setCurrentTab, selectedTag, currentTab }) => {
  const [tags, setTags] = useState(selectedTag ? [selectedTag] : []);
  const [pagination, dispatch] = useReducer((state, action) => {
    if (action.type === "reset") {
      return getPagination(currentTab);
    }
    return {
      ...state,
      [action.type]: action.value
    };
  }, getPagination(currentTab));
  const onSelectTag = tag => {
    if (_findIndex(tags, t => t.id === tag.id) >= 0) {
      setTags(_filter(tags, t => t.id !== tag.id));
    } else {
      setTags([...tags, tag]);
    }
  };

  const onUpdatePagination = (key, value) => {
    dispatch({ type: key, value });
  };

  const onSearchMore = tab => {
    setCurrentTab(tab);
  };
  return { tags, pagination, onSelectTag, onUpdatePagination, onSearchMore };
};

const SearchPageResult = ({
  searchKeyword,
  currentTab,
  onGoTo,
  searchTags,
  setCurrentTab,
  selectedTag
}) => {
  const {
    tags,
    onSelectTag,
    onUpdatePagination,
    pagination,
    onSearchMore
  } = useSearchResult({ setCurrentTab, selectedTag, currentTab });
  const tagIds = _map(tags, t => t.id);
  return (
    <PageItem
      key={searchKeyword}
      variables={{
        searchType: currentTab,
        searchKeyword,
        tagIds,
        ...pagination
      }}
      gql={SEARCH_ITEMS}
    >
      {({ loading, refetch, item }) => {
        return (
          <_SearchPageResult
            loading={loading}
            currentTab={currentTab}
            onSelectTag={onSelectTag}
            searchResult={item}
            onUpdatePagination={onUpdatePagination}
            pagination={pagination}
            searchTags={searchTags}
            refetchBook={refetch}
            onGoTo={onGoTo}
            tagIds={tagIds}
            tags={tags}
            onSearchMore={onSearchMore}
          />
        );
      }}
    </PageItem>
  );
};

const SearchPage = ({ onGoTo, location }) => {
  const {
    currentTab,
    setCurrentTab,
    searchKeyword,
    selectedTag
  } = useSearchPage({
    location
  });
  return (
    <Container>
      <Tabs currentTab={currentTab} onSwitchTab={tab => setCurrentTab(tab)} />
      <PageItem
        autoLoading
        variables={{
          searchKeyword
        }}
        itemPath={"tags.items"}
        gql={GET_SEARCH_TAGS}
      >
        {({ loading, refetch, item }) => {
          return (
            <SearchPageResult
              loading={loading}
              searchTags={item}
              selectedTag={selectedTag}
              currentTab={currentTab}
              searchKeyword={searchKeyword}
              refetchBook={refetch}
              onGoTo={onGoTo}
              setCurrentTab={setCurrentTab}
            />
          );
        }}
      </PageItem>
    </Container>
  );
};

export default SearchPage;
