import React, { useEffect, useState, useReducer } from "react";
import {
  Modal,
  Header,
  Icon,
  Button,
  Radio,
  TextArea
} from "semantic-ui-react";
import { useQuery } from "react-apollo";
import { GET_REPORT_ITEMS_GROUPS } from "./queryGql";
import _get from "lodash/get";
import _map from "lodash/map";
import _filter from "lodash/filter";
import _isEmpty from "lodash/isEmpty";
import styled from "styled-components";
import { useMutation } from "react-apollo";
import { SAVE_REPORT } from "./mutationGql";
import ModalHint from "./ModalHint";
import ToastHint from "./ToastHint";

const GroupItemBox = styled.div`
  margin-bottom: 20px;
  pointer-events: ${props => (props.disabled ? "none" : "")};
`;

const GroupTitle = styled.div`
  color: #99a2aa;
  margin-bottom: 12px;
`;

const GroupContent = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const GroupItem = ({ group, ids, onUpdateItem, disabled }) => {
  const { name, reportItems } = group;
  return (
    <GroupItemBox disabled={disabled}>
      <GroupTitle>{name}</GroupTitle>
      <GroupContent>
        {_map(reportItems, item => {
          const { id, name } = item;
          const checked = ids.indexOf(id) >= 0;
          return (
            <Radio
              key={"report_item_" + item.id}
              type={"radio"}
              label={name}
              style={{ marginRight: 30 }}
              value={id}
              onClick={() => {
                onUpdateItem(id, !checked);
              }}
              disabled={disabled}
              checked={checked}
            />
          );
        })}
      </GroupContent>
    </GroupItemBox>
  );
};

const InputReasonBox = styled.div`
  display: flex;
  flex-direction: column;
  pointer-events: ${props => (props.disabled ? "none" : "")};
`;

const InputText = styled(TextArea)`
  margin-top: 10px;
  width: 100%;
  padding: 6px;
`;

const InputReason = ({ reason, onUpdateReason, disabled }) => {
  const hasReasonAndDisabled = disabled && reason;
  const [show, setShow] = useState(hasReasonAndDisabled);
  if (disabled && !reason) {
    return null;
  }
  return (
    <InputReasonBox disabled={disabled}>
      <Radio
        type={"radio"}
        label={""}
        disabled={disabled}
        checked={show}
        onChange={(e, { checked }) => {
          setShow(checked);
        }}
        onClick={() => setShow(!show)}
      />
      {show && (
        <InputText
          disabled={disabled}
          placeholder={""}
          value={reason}
          maxLength={"255"}
          onChange={(e, { value }) => {
            onUpdateReason(value);
          }}
        />
      )}
    </InputReasonBox>
  );
};

const PanelBox = styled.div``;

const ReportPanel = ({
  groups,
  disabled,
  ids,
  onUpdateItem,
  reportReason,
  onUpdateReason
}) => {
  return (
    <PanelBox>
      {_map(groups, g => {
        return (
          <GroupItem
            key={"report_items_group_" + g.id}
            disabled={disabled}
            group={g}
            ids={ids}
            onUpdateItem={onUpdateItem}
          />
        );
      })}
      <InputReason
        onUpdateReason={onUpdateReason}
        reason={reportReason}
        disabled={disabled}
      />
    </PanelBox>
  );
};

const useReport = ({ currentReport, onHide, reportCallback }) => {
  const [report, setReport] = useState(currentReport);
  const [reportReason, setReportReason] = useState(
    _get(currentReport, "reportReason")
  );
  const [ids, setIds] = useState(
    _map(_get(currentReport, "items", []), item => item.id)
  );
  const onUpdateItem = (id, isAdd) => {
    if (isAdd) {
      setIds([...ids, id]);
    } else {
      setIds(_filter(ids, i => i !== id));
    }
  };

  const onUpdateReason = reason => {
    setReportReason(reason);
  };

  const [onSaveReport, { loading: mLoading }] = useMutation(SAVE_REPORT, {
    onCompleted: data => {
      const report = _get(data, "saveReport");
      setReport(report);
      onHide && onHide();
      reportCallback && reportCallback(report);
      ToastHint.showSuccess("");
    },
    onError: () => ModalHint.error("")
  });

  return {
    ids,
    report,
    onSaveReport,
    onUpdateItem,
    reportReason,
    onUpdateReason,
    mLoading
  };
};

const _ReportModal = ({
  title,
  onHide,
  currentReport,
  reportCallback,
  groups,
  objId,
  type
}) => {
  const {
    report,
    onSaveReport,
    ids,
    onUpdateItem,
    reportReason,
    onUpdateReason,
    mLoading
  } = useReport({
    currentReport,
    onHide,
    reportCallback
  });
  return (
    <Modal
      style={{ top: 200 }}
      centered={false}
      closeIcon
      open={true}
      onClose={onHide}
    >
      <Header icon="info circle" content={title} />
      <Modal.Content>
        <ReportPanel
          ids={ids}
          disabled={report}
          groups={groups}
          onUpdateItem={onUpdateItem}
          onUpdateReason={onUpdateReason}
          reportReason={reportReason}
        />
      </Modal.Content>
      <Modal.Actions>
        <Button
          primary
          loading={mLoading}
          disabled={(_isEmpty(ids) && !reportReason) || report}
          color="red"
          onClick={() =>
            onSaveReport({
              variables: { itemIds: ids, type, objId, reportReason }
            })
          }
        >
          {report ? "" : ""}
        </Button>
      </Modal.Actions>
    </Modal>
  );
};

const ReportModal = ({
  type,
  objId,
  title = "",
  currentReport,
  onHide,
  reportCallback
}) => {
  const { loading, error, data, refetch, networkStatus } = useQuery(
    GET_REPORT_ITEMS_GROUPS,
    {
      variables: {
        types: ["REPORT_ITEM_GROUP"],
        sort: "codeRank",
        order: "desc"
      }
    }
  );
  const groups = _get(data, "codes.items", []);
  return (
    <_ReportModal
      title={title}
      reportCallback={reportCallback}
      currentReport={currentReport}
      onHide={onHide}
      groups={groups}
      type={type}
      objId={objId}
    />
  );
};

export default ReportModal;
