import React from "react";
import styled from "styled-components";
import ProgramRecommendItem from "../../item/ProgramRecommendItem";
import FlatList from "../../common/FlatList";
import _get from "lodash/get";

const Container = styled.div`
  margin-left: 10px;
`;

const ProgramTopGridVRecommends = ({ grid, onGoTo }) => {
  return (
    <Container>
      <FlatList
        items={_get(grid, "rightRecommends", [])}
        itemWidth={290}
        columnsInRow={1}
        gapY={"6px"}
        renderItem={({ item }) => {
          return (
            <ProgramRecommendItem
              item={item}
              key={Math.random().toString()}
              onGoTo={onGoTo}
            />
          );
        }}
      />
    </Container>
  );
};

export default ProgramTopGridVRecommends;
