import React from "react";
import styled from "styled-components";
import SwipeItems from "../common/SwipeItems";
import _get from "lodash/get";
import BlogTopGridVideoBlogRecommends from "./blog/BlogTopGridVideoBlogRecommends";
import BlogTopGridArticleBlogRecommends from "./blog/BlogTopGridArticleBlogRecommends";
import Space from "../../../common/Space";

const Container = styled.div`
  display: flex;
`;

const LeftRecommendsBox = styled.div`
  width: 220px;
`;

const RightRecommendsBox = styled.div`
  width: 280px;
`;

const BlogTopGrid = props => {
  const { grid, onGoTo } = props;
  return (
    <Container>
      <LeftRecommendsBox>
        <BlogTopGridVideoBlogRecommends grid={grid} onGoTo={onGoTo} />
      </LeftRecommendsBox>
      <Space width={"12px"} />
      <SwipeItems
        width={800}
        height={400}
        items={_get(grid, "items", [])}
        onGoTo={onGoTo}
      />
      <RightRecommendsBox>
        <BlogTopGridArticleBlogRecommends grid={grid} onGoTo={onGoTo} />
      </RightRecommendsBox>
    </Container>
  );
};

export default BlogTopGrid;
