import React from "react";
import styled from "styled-components";
import AllPageAdBannerItem from "../item/AllPageAdBannerItem";
import _get from "lodash/get";
import _head from "lodash/head";

const Container = styled.div``;

const AllPageAdBanner = ({ grid }) => {
  const item = _head(_get(grid, "items"));
  return (
    <Container>
      <AllPageAdBannerItem item={item} />
    </Container>
  );
};

export default AllPageAdBanner;
