import { currentPath } from "./pathUtils";
import _split from "lodash/split";
import _last from "lodash/last";

export const getRouteId = () => {
  const path = currentPath();
  const lastPart = _last(_split(path, "/"));
  let id = 0;
  try {
    id = parseInt(lastPart);
  } catch (e) {
    console.error("wrong id :", id);
  }
  return id;
};

export const getRouteLastValue = () => {
  const path = decodeURI(currentPath());
  return _last(_split(path, "/"));
};

export const getSearchKeyword = () => {
  const path = currentPath();
  if (path.indexOf("/search") >= 0) {
    return getRouteLastValue();
  }
  return "";
};
