import React from "react";
import styled from "styled-components";
import SearchContent from "./SearchContent";
import BookItem from "./items/BookItem";
import FlatList from "../../../common/FlatList";

const Container = styled.div`
  margin-right: 40px;
`;

const BooksBox = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const BookPanel = ({
  currentTab,
  pagination,
  books,
  onUpdatePagination,
  onGoTo,
  onSearchMore,
  isAll
}) => {
  const { count, items } = books;
  if (isAll && count <= 0) {
    return null;
  }
  return (
    <Container>
      <SearchContent
        currentTab={currentTab}
        pagination={pagination}
        itemsCount={count}
        title={""}
        tab={"Book"}
        emptyText={""}
        onSearchMore={onSearchMore}
        onUpdatePagination={onUpdatePagination}
      >
        <BooksBox>
          <FlatList
            items={items}
            columns={4}
            itemWidth={"220px"}
            rowGap={"18px"}
          >
            {({ item: book, isLastRow }) => {
              return (
                <BookItem
                  key={"book___" + book.id}
                  item={book}
                  onGoTo={onGoTo}
                />
              );
            }}
          </FlatList>
        </BooksBox>
      </SearchContent>
    </Container>
  );
};

export default BookPanel;
