import React from "react";
import styled from "styled-components";
import SwipeItems from "../common/SwipeItems";
import _get from "lodash/get";
import HomeSwipeGridRecommends from "./home/HomeSwipeGridRecommends";

const Container = styled.div`
  display: flex;
`;

const HomeSwipeGrid = ({ grid, onGoTo }) => {
  console.log("grid_________________", grid);
  return (
    <Container>
      <SwipeItems
        width={1000}
        onGoTo={onGoTo}
        height={475}
        items={_get(grid, "items", [])}
      />
      <HomeSwipeGridRecommends recommends={_get(grid, "recommends", [])} />
    </Container>
  );
};

export default HomeSwipeGrid;
