import React from "react";
import styled from "styled-components";
import ProblemForm from "./ProblemForm";
import { GET_PROBLEM } from "../../common/queryGql";
import { getRouteId } from "../../utils/routeUtils";
import PageItem from "../QueryItem";
import PageTitle from "../../common/PageTitle";

const Container = styled.div`
  margin-top: 40px;
`;

const ProblemEditPage = ({ history }) => {
  return (
    <Container>
      <PageItem id={getRouteId()} itemPath={"problem"} gql={GET_PROBLEM}>
        {({ loading, refetch, item }) => {
          return (
            <ProblemForm
              isEdit={true}
              history={history}
              refetch={refetch}
              formData={item}
              loading={loading}
            />
          );
        }}
      </PageItem>
    </Container>
  );
};

export default ProblemEditPage;
