import React, { useEffect, useState, useReducer } from "react";
import ErrorModal from "./ErrorModal";
import _noop from "lodash/noop";
import _toLower from "lodash/toLower";
import _get from "lodash/get";
import styled from "styled-components";
import { Confirm } from "semantic-ui-react";
const ERROR = "error";
const INFO = "info";
const CONFIRM = "info";

const _ModalHint = ({ onShow, setOnShow }) => {
  const [message, setMessage] = useState("");
  const [config, setConfig] = useState({});
  const [type, dispatch] = useReducer((state, action) => {
    return _toLower(action);
  }, "");
  if (!onShow) {
    setOnShow((action, msg, mConfig) => {
      dispatch(action);
      setMessage(msg);
      if (mConfig) {
        setConfig(mConfig);
      }
    });
  }
  const onHide = () => {
    dispatch("");
  };

  const onOk = _get(config, "onOk", _noop);

  const { title } = config;
  switch (type) {
    case ERROR:
      return (
        <ErrorModal
          errorTitle={title}
          open={!!type}
          message={message}
          onHide={onHide}
        />
      );
    case INFO:
    case CONFIRM:
      return (
        <Confirm
          style={{ top: 300 }}
          open={!!type}
          header={{ content: _get(config, "title") }}
          content={_get(config, "content")}
          cancelButton={{ content: _get(config, "cancelText", "") }}
          confirmButton={{
            content: _get(config, "okText", ""),
            loading: _get(config, "loading", false)
          }}
          onConfirm={() => onOk(onHide)}
          onCancel={onHide}
        />
      );
    default:
      return null;
  }
};

class ModalHint {
  static onShow = null;

  static setOnShow = onShowFnc => {
    this.onShow = onShowFnc;
  };

  static hintComponent = (
    <_ModalHint onShow={this.onShow} setOnShow={this.setOnShow} />
  );
  static render() {
    return this.hintComponent;
  }

  static error(msg, options, onOk) {
    this.onShow(ERROR, msg, { ...options, onOk });
  }
  static info(msg) {}

  static confirm(options, onOk) {
    this.onShow(CONFIRM, "", { ...options, onOk });
  }
}
export default ModalHint;
