import React from "react";
import styled from "styled-components";
import { Pagination } from "semantic-ui-react";
import SearchEmpty from "./SearchEmpty";

const Container = styled.div`
  padding-bottom: 60px;
`;

const Content = styled.div``;

const Title = styled.div`
  text-align: left;
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 20px;
`;

const More = styled.div`
  color: #999;
  cursor: pointer;
  &:hover {
    color: lightblue;
  }
`;

const HeaderBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const Header = ({ title, totalPages, tab, onSearchMore }) => {
  return (
    <HeaderBox>
      <Title>{title}</Title>
      {totalPages > 1 && (
        <More onClick={() => onSearchMore(tab)}></More>
      )}
    </HeaderBox>
  );
};

const PaginationBox = styled.div`
  margin-top: 40px;
`;

const SearchContent = ({
  itemsCount,
  onUpdatePagination,
  pagination,
  currentTab,
  title,
  tab,
  onSearchMore,
  emptyText,
  children
}) => {
  const { limit, offset } = pagination;
  const totalPages = itemsCount / limit;
  return (
    <Container>
      {currentTab === "All" && (
        <Header
          tab={tab}
          totalPages={totalPages}
          onSearchMore={onSearchMore}
          title={title}
        />
      )}
      <Content>
        {itemsCount > 0 ? children : <SearchEmpty text={emptyText} />}
      </Content>
      {currentTab !== "All" && totalPages > 1 && (
        <PaginationBox>
          <Pagination
            activePage={offset / limit + 1}
            totalPages={totalPages}
            onPageChange={(event, { activePage }) => {
              onUpdatePagination("offset", (activePage - 1) * limit);
            }}
          />
        </PaginationBox>
      )}
    </Container>
  );
};

export default SearchContent;
