import React, { useState, useReducer } from "react";
import _get from "lodash/get";
import ReportModal from "./ReportModal";
import LoginHint from "./LoginHint";

const _ReportHint = ({ onShow, setOnShow }) => {
  const [variables, setVariables] = useState({});
  const [config, setConfig] = useState({});
  const [currentReport, setCurrentReport] = useState(null);
  const [open, setOpen] = useState(false);
  if (!onShow) {
    setOnShow((reportType, objId, currentReport, rConfig) => {
      setOpen(true);
      setVariables({ objId, type: reportType, id: _get(currentReport, "id") });
      setConfig(rConfig);
      setCurrentReport(currentReport);
    });
  }
  const onHide = () => {
    setOpen(false);
  };
  const { title } = config;

  return open ? (
    <ReportModal
      title={title}
      {...variables}
      open={open}
      onHide={onHide}
      currentReport={currentReport}
      reportCallback={config.reportCallback}
    />
  ) : null;
};

class ReportHint {
  static onShow = null;

  static setOnShow = onShowFnc => {
    this.onShow = onShowFnc;
  };
  static hintComponent = (
    <_ReportHint onShow={this.onShow} setOnShow={this.setOnShow} />
  );
  static render() {
    return this.hintComponent;
  }

  static show(reportType, objId, currentReport, rConfig) {
    LoginHint.checkLogin(() =>
      this.onShow(reportType, objId, currentReport, rConfig)
    );
  }
}
export default ReportHint;
