import React from "react";
import styled from "styled-components";
import ArticleRecommend from "./ArticleRecommend";
import _get from "lodash/get";
import FlatList from "../../common/FlatList";

const Container = styled.div``;

const BlogTopGridArticleBlogRecommends = ({
  grid,
  itemsKey = "articleRecommends",
  onGoTo
}) => {
  const items = _get(grid, itemsKey, []);
  return (
    <Container>
      <FlatList
        columnsInRow={1}
        items={items}
        gapY={"10px"}
        isFlexItem={true}
        renderItem={({ item, isLastOne }) => {
          return (
            <ArticleRecommend
              key={Math.random().toString()}
              item={item}
              onGoTo={onGoTo}
              isLastOne={isLastOne}
            />
          );
        }}
      />
    </Container>
  );
};

export default BlogTopGridArticleBlogRecommends;
