import React, { useState, useEffect, useReducer } from "react";
import { Form, Input } from "semantic-ui-react";
import styled from "styled-components";
import _map from "lodash/map";
import _get from "lodash/get";
import _isEmpty from "lodash/isEmpty";
import _filter from "lodash/filter";
import _find from "lodash/find";
import _pick from "lodash/pick";
import _noop from "lodash/noop";
import { validateForm } from "./validate";
import _toLower from "lodash/toLower";

const Container = styled.div`
  .ui.form .field > label {
    text-align: left;
  }
  .ui.form .field .prompt.label {
    float: left;
  }
`;

const getControlByType = type => {
  let inputType = "input";
  switch (_toLower(type)) {
    case "password":
      inputType = "password";
    case "input":
    default:
      return ({ value, onChange, placeholder }) => (
        <Input
          defaultValue={value}
          type={inputType}
          onChange={(e, { name, value }) => {
            onChange(value);
          }}
          placeholder={placeholder}
        />
      );
  }
};

const FormItem = ({
  item,
  form,
  onUpdateField,
  error,
  onValidate,
  setErrors
}) => {
  const { label, placeholder, renderControl, fieldKey, required, type } = item;
  const value = _get(form, fieldKey, null);
  console.log("value_________________________AAA", value);
  const [state, dispatch] = useReducer((state, action) => {
    return { ...state, [action.key]: action.value };
  }, {});
  let control;
  const onChange = value => onUpdateField(fieldKey, value);
  if (typeof renderControl === "function") {
    control = () =>
      renderControl({
        item,
        value,
        onUpdateField,
        onChange,
        form,
        setErrors,
        onSetError: errorMsg => {
          setErrors({ [item.fieldKey]: errorMsg });
        },
        formItemState: state,
        onUpdateState: (key, value) => dispatch({ key, value }),
        error,
        onValidate
      });
  } else {
    control = () =>
      getControlByType(type)({
        value,
        onChange,
        form,
        error,
        setErrors,
        onSetError: errorMsg => {
          setErrors({ [item.fieldKey]: errorMsg });
        },
        formItemState: state,
        onUpdateState: (key, value) => dispatch({ key, value }),
        placeholder,
        onValidate
      });
  }

  const fieldErrorProps = error
    ? { error: { pointing: "below", content: error } }
    : {};
  return (
    <Form.Field
      label={label}
      required={required}
      placeholder={placeholder}
      control={control}
      {...fieldErrorProps}
    />
  );
};

const useForm = ({ formData, items }) => {
  const [refreshForm, setRefreshForm] = useState("refresh");
  const [formStore, setFormStore] = useState(formData);
  let form = formStore;
  const [errors, setErrors] = useState({});
  const updateField = (fieldKey, value) => {
    form[fieldKey] = value;
  };
  return {
    form,
    onUpdateField: updateField,
    errors,
    setErrors: errors => {
      setFormStore(form);
      setErrors(errors);
    },
    refreshForm,
    setRefreshForm,
    onValidate: (submit, validateKeys) => {
      const errors = validateForm(
        form,
        _isEmpty(validateKeys)
          ? items
          : _filter(items, item => validateKeys.indexOf(item.fieldKey) >= 0)
      );
      if (errors) {
        setErrors(errors);
      } else {
        setErrors(null);
        submit && submit();
      }
    }
  };
};

const BasicForm = ({
  loading,
  items,
  width,
  height,
  children,
  formData = {}
}) => {
  const {
    form,
    onUpdateField,
    errors,
    setErrors,
    refreshForm,
    onValidate
  } = useForm({
    formData,
    items
  });
  return (
    <Container style={{ width, height }} key={refreshForm}>
      <Form loading={loading}>
        {_map(items, item => {
          return (
            <FormItem
              key={item.label}
              form={form}
              item={item}
              setErrors={setErrors}
              onValidate={onValidate}
              error={_get(errors, item.fieldKey)}
              onUpdateField={onUpdateField}
            />
          );
        })}
      </Form>
      {children &&
        children({
          form,
          onSubmit: () => {},
          onAddErrors: setErrors,
          onValidate: onValidate
        })}
    </Container>
  );
};

export default BasicForm;
