import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Avatar from "../../pages/transformable/common/Avatar";
import _get from "lodash/get";
import { Icon, Popup } from "semantic-ui-react";
import { _getByRank } from "../../utils/utils";
import { getOptionByKey } from "../../utils/optionsUtils";
import { PROBLEM_STATUS } from "../OPTIONS";
import { PROBLEM_TYPE } from "../CONSULT";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 26px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.3);
  padding: 10px 20px;
  width: 100%;
`;

const HeaderBox = styled.div`
  width: 100%;
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
`;

const Title = styled.div`
  font-size: 16px;
  font-weight: bold;
  text-align: left;
`;

const Header = ({ type, title, status }) => {
  const { label, bColor } = getOptionByKey(PROBLEM_STATUS, "value", status);
  return (
    <HeaderBox>
      <Title>{title}</Title>
      <Status bColor={bColor}>{label}</Status>
    </HeaderBox>
  );
};

const Status = styled.div`
  background-color: ${props => props.bColor};
  color: white;
  width: 60px;
  font-size: 14px;
  line-height: 24px;
  border-radius: 12px;
  height: 24px;
`;

const Content = styled.div`
  margin-bottom: 16px;
`;

const FooterBox = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 6px;
`;

const FooterItem = styled.div`
  display: flex;
  align-items: center;
  margin-right: 20px;
`;

const Text = styled.div`
  margin-left: 16px;
`;

const Footer = ({ item }) => {
  const typeOption = getOptionByKey(PROBLEM_TYPE, "value", item.type);
  return (
    <FooterBox>
      <FooterItem>
        <Avatar url={_get(item, "creator.avatar.url", "")} />
        <Text>
          {_getByRank(item.creator, ["nickname", "username"], "-username-")}
        </Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"calendar alternate outline"} />
        <Text>{_get(item, "lastUpdated", "-date-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"heart outline"} />
        <Text>{_get(item, "upCount", "-like-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"eye"} />
        <Text>{_get(item, "readCount", "-read-")}</Text>
      </FooterItem>
      <FooterItem>
        <Popup
          content={typeOption.label}
          trigger={
            <Icon
              name={typeOption.value === "Bug" ? "bug" : "envelope open"}
              style={{ color: typeOption.bgColor, fontSize: 16 }}
            />
          }
        />
      </FooterItem>
    </FooterBox>
  );
};

const ContentText = styled.div`
  text-align: left;
`;

const ActionsBox = styled.div``;

const ProblemItem = ({ problem }) => {
  const { id, type, title, simpleDescription, status } = problem;

  return (
    <Container>
      <Header type={type} title={title} status={status} />
      <Content>
        <ContentText>{simpleDescription}</ContentText>
      </Content>
      <Footer item={problem} />
    </Container>
  );
};

export default ProblemItem;
