import React from "react";
import styled from "styled-components";
import RecommendTags from "./RecommendTags";
import SearchHistory from "./SeachHistory";
import Tags from "./Tags";
import BlogPanel from "./BlogPanel";
import ProgramPanel from "./ProgramPanel";
import BookPanel from "./BookPanel";
import _each from "lodash/each";
import _get from "lodash/get";
import SearchEmpty from "./SearchEmpty";

const Container = styled.div`
  display: flex;
  justify-content: space-between;
`;

const MainBox = styled.div`
  flex: 1;
`;

const RightBox = styled.div`
  width: 300px;
`;

const checkTab = (currentTab, panelTab) => {
  return currentTab === "All" || currentTab === panelTab;
};

const isAllEmpty = (result, fieldKeys) => {
  let isEmpty = true;
  _each(fieldKeys, fieldKey => {
    if (_get(result, `${fieldKey}.count`, 0) > 0) {
      isEmpty = false;
    }
  });
  return isEmpty;
};

const SearchResultPanel = ({
  pagination,
  onUpdatePagination,
  searchTags,
  onGoTo,
  searchResult,
  tagIds,
  tags,
  currentTab,
  onSelectTag,
  onSearchMore
}) => {
  const commonProps = { pagination, currentTab, onGoTo, onUpdatePagination };
  const isAll = currentTab === "All";
  return (
    <Container>
      <MainBox>
        <Tags tags={tags} onSelectTag={onSelectTag} />
        {checkTab(currentTab, "Blog") && (
          <BlogPanel
            {...commonProps}
            blogs={searchResult["blogs"]}
            onSearchMore={onSearchMore}
            isAll={isAll}
          />
        )}
        {checkTab(currentTab, "Program") && (
          <ProgramPanel
            {...commonProps}
            onSearchMore={onSearchMore}
            isAll={isAll}
            programs={searchResult["programs"]}
          />
        )}
        {checkTab(currentTab, "Book") && (
          <BookPanel
            {...commonProps}
            isAll={isAll}
            onSearchMore={onSearchMore}
            books={searchResult["books"]}
          />
        )}
        {isAll && isAllEmpty(searchResult, ["books", "blogs", "programs"]) && (
          <SearchEmpty text={""} />
        )}
      </MainBox>
      <RightBox>
        <RecommendTags
          selectedTagIds={tagIds}
          tags={searchTags}
          onSelectTag={onSelectTag}
        />
        <SearchHistory onGoTo={onGoTo} />
      </RightBox>
    </Container>
  );
};

export default SearchResultPanel;
