import React from "react";
import styled from "styled-components";
import Avatar from "../common/Avatar";
import _get from "lodash/get";
import Tags from "../common/Tags";
import { BOOK } from "../../../common/CONSULT";

const Container = styled.div`
  display: flex;
  border-bottom: 1px solid
    ${props => (props.isLastOne ? "transparent" : "#cecece")};
  padding-bottom: 20px;
  margin-bottom: 16px;
  cursor: pointer;
  width: 100%;

  &:hover {
    box-shadow: 0px 7px 7px -7px #bbb;
    border-bottom: 1px solid transparent;
    .book_name {
      color: #fb6b84;
    }
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
  }
`;

const CoverImage = styled.img`
  border-top-right-radius: 6px;
  border-bottom-right-radius: 4px;
`;

const ContentBox = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 18px;
  width: 100%;
`;

const Title = styled.div`
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 10px;
  margin-top: 6px;
  align-self: flex-start;
`;

const Description = styled.div`
  color: gray;
  flex: 1;
`;

const FooterBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const LeftBox = styled.div``;

const RightBox = styled.div`
  margin-right: 20px;
`;

const FooterItem = styled.div`
  display: flex;
`;

const TextCount = styled.div`
  color: #666;
`;

const Text = styled.div`
  margin-left: 16px;
  color: #666;
  font: 14px / 24px "Microsoft YaHei", , SimSun, tahoma, arial;
`;

const Footer = ({ item }) => {
  const tags = _get(item, "tags", []);
  return (
    <FooterBox>
      <LeftBox>
        <FooterItem>
          <Avatar url={_get(item, "creator.avatar.url", "")} />
          <Text>{_get(item, "creator.username", "-username-")}</Text>
        </FooterItem>
      </LeftBox>
      <RightBox>
        <FooterItem>
          <Tags tags={tags} />
        </FooterItem>
        <FooterItem>
          <TextCount>250</TextCount>
        </FooterItem>
      </RightBox>
    </FooterBox>
  );
};

const BookListItem = ({ item, isLastOne, onGoTo }) => {
  const {
    id,
    name,
    intro,
    cover: { url }
  } = item;
  return (
    <Container onClick={() => onGoTo(`${BOOK}${id}`)} isLastOne={isLastOne}>
      <CoverImage className={"cover_img"} width={120} height={154} src={url} />
      <ContentBox>
        <Title className={"book_name"}>{name}</Title>
        <Description>{intro}</Description>
        <Footer item={item} />
      </ContentBox>
    </Container>
  );
};

export default BookListItem;
