import React from "react";
import styled from "styled-components";
import _map from "lodash/map";
import { Link } from "react-router-dom";
import { SEARCH } from "../../../common/CONSULT";

const COLOR_LIST = [
  "magenta",
  "red",
  "lightgreen",
  "orange",
  "gold",
  "lime",
  "green",
  "cyan",
  "blue",
  "pink",
  "purple"
];

const getRandomColor = () => {
  const randomIndex = parseInt((Math.random() * 11).toString());
  return COLOR_LIST[randomIndex];
};

const TagBox = styled.div`
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  color: ${props => props.color};
  border: 1px solid ${props => props.color};
  padding: 4px 8px;
  &:hover {
    box-shadow: 0 2px 4px ${props => props.color};
  }
`;

const TagText = styled.div`
  color: ${props => props.color};
`;

const Tag = ({ tag }) => {
  const { id, name, type } = tag;
  const color = getRandomColor();
  return (
    <TagBox style={{ borderRadius: 18, margin: 6 }} color={color}>
      <TagText color={color}>{name}</TagText>
    </TagBox>
  );
};

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const Tags = ({ tags }) => {
  return (
    <Container>
      {_map(tags, (tag, index) => {
        return (
          <Link
            key={"tag_" + index}
            to={{
              pathname: SEARCH,
              state: {
                selectedTag: tag
              }
            }}
          >
            <Tag tag={tag} />
          </Link>
        );
      })}
    </Container>
  );
};

export default Tags;
