import React from "react";
import styled from "styled-components";
import SearchContent from "./SearchContent";
import _map from "lodash/map";
import BlogItem from "./items/BlogItem";

const Container = styled.div`
  margin-right: 40px;
`;

const BlogPanel = ({
  currentTab,
  pagination,
  blogs,
  onUpdatePagination,
  onGoTo,
  onSearchMore,
  isAll
}) => {
  const { count, items } = blogs;
  if (isAll && count <= 0) {
    return null;
  }
  return (
    <Container>
      <SearchContent
        currentTab={currentTab}
        pagination={pagination}
        itemsCount={count}
        title={""}
        emptyText={""}
        tab={"Blog"}
        onSearchMore={onSearchMore}
        onUpdatePagination={onUpdatePagination}
      >
        {_map(items, (blog, index) => {
          return (
            <BlogItem
              key={"blog___" + blog.id}
              item={blog}
              onGoTo={onGoTo}
              isLastOne={index + 1 === items.length}
            />
          );
        })}
      </SearchContent>
    </Container>
  );
};

export default BlogPanel;
