import React from "react";
import styled from "styled-components";
import _chunk from "lodash/chunk";
import _map from "lodash/map";
import Space from "./Space";

const RowBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;
const ItemBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  ${props => (props.itemWidth ? `width:${props.itemWidth}` : " flex: 1;")};
`;

const Row = ({ row, children, isLastRow, columns, cGap, itemWidth }) => {
  if (isLastRow) {
    const rowLength = row.length;
    for (let i = 0; i < columns - rowLength; i++) {
      row.push({ id: Math.random(), isEmpty: true });
    }
  }

  return (
    <RowBox>
      {_map(row, item => {
        return isLastRow ? (
          <>
            <ItemBox key={item.id} itemWidth={itemWidth}>
              {!item.isEmpty ? children && children({ item, isLastRow }) : null}
            </ItemBox>
            {cGap && <Space width={cGap} />}
          </>
        ) : (
          <>
            <ItemBox key={item.id} itemWidth={itemWidth}>
              {children && children({ item, isLastRow })}
            </ItemBox>
            {cGap && <Space width={cGap} />}
          </>
        );
      })}
    </RowBox>
  );
};

const Container = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
`;

const FlatList = ({
  items,
  columns = 3,
  children,
  rowClass,
  className,
  itemWidth,
  rowGap,
  columnGap,
  gap
}) => {
  const rows = _chunk(items, columns);
  const rGap = rowGap || gap;
  const cGap = columnGap || gap;
  return (
    <Container className={className}>
      {_map(rows, (row, index) => {
        return (
          <>
            <Row
              columns={columns}
              isLastRow={index + 1 === rows.length}
              className={rowClass}
              key={"row_" + row.id}
              row={row}
              cGap={cGap}
              children={children}
              itemWidth={itemWidth}
            />
            {rGap && <Space height={rGap} />}
          </>
        );
      })}
    </Container>
  );
};

export default FlatList;
