import React from "react";
import styled from "styled-components";
import Avatar from "../../common/Avatar";
import { Link } from "react-router-dom";
import { Tooltip } from "antd";

const Container = styled.div`
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  margin-bottom: 10px;
  padding-top: 20px;
  padding-bottom: 30px;
  border-bottom: 1px solid #f0c5c5;
  margin-right: 10px;
  margin-left: 10px;
  &:hover {
    .recommend_text {
      color: #ed4259;
    }
  }
`;

const RecommendText = styled.div`
  font-size: 16px;
  height: 34px;
  line-height: 34px;
  color: #333333;
`;

const LabelBox = styled.div`
  height: 34px;
  width: 34px;
  line-height: 34px;
  text-align: center;
  background-color: ${props => props.bgColor};
  color: ${props => props.color};
  margin-right: 10px;
  font-size: 20px;
`;

const getBgColor = index => {
  switch (index) {
    case 0:
      return "#F43636";
    case 1:
      return "#F2A56C";
    case 2:
      return "#E6BF25";
    default:
      return "#cecece";
  }
};

const RecommendLabel = ({ index }) => {
  return (
    <LabelBox
      bgColor={getBgColor(index)}
      color={index < 3 ? "white" : "#575252"}
    >
      {index + 1}
    </LabelBox>
  );
};

const ItemBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
`;

const RecommendContent = styled.div`
  display: flex;
  align-items: center;
`;

const RecommendItem = ({ recommend, index }) => {
  const { recommendText, creator, linkUrlItem, linkUrl } = recommend;
  const recommendLengthLargerThan10 =
    recommendText && recommendText.length > 12;
  return (
    <Container>
      <ItemBox>
        <Link to={linkUrl}>
          <RecommendContent>
            <RecommendLabel index={index} />
            <RecommendText className={"recommend_text"}>
              {recommendLengthLargerThan10 ? (
                <Tooltip title={recommendText}>
                  {recommendText.slice(0, 12)}...
                </Tooltip>
              ) : (
                recommendText
              )}
            </RecommendText>
          </RecommendContent>
        </Link>
        <Avatar avatar={creator.avatar} />
      </ItemBox>
    </Container>
  );
};

export default RecommendItem;
