import React, { useEffect, useState } from "react";
import { Modal, Button, Form, Input } from "semantic-ui-react";
import styled from "styled-components";
import BasicForm from "../../common/BasicForm";
import { clickEle } from "../../utils/elementLink";
import { SERVER_URL } from "../../../config";
import axios from "axios";
import { refreshCurrentPage } from "../../utils/pathUtils";
import LoginHint from "../../common/LoginHint";
import CaptchaInput from "../../common/formFields/CaptchaInput";
import { ACTION_TYPE, CAPTCHA_TYPE } from "../../common/CONSULT";
import _get from "lodash/get";
import _join from "lodash/join";
import isEmail from "validator/lib/isEmail";
import isPhone from "validator/lib/isMobilePhone";

const LOGIN_SPLIT_TAG = "_#_";

export const login = (username, password, setAuthentication) => {
  return axios
    .post(SERVER_URL + "/login", { username, password })
    .then(response => {
      setAuthentication(response.data);
      refreshCurrentPage();
    });
};

const RegisterButton = styled.div`
  cursor: pointer;
  margin-top: 14px;
  color: #4183c4;
`;

const FooterBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const CaptchaLoginButton = styled.div`
  cursor: pointer;
  margin-top: 14px;
  color: #4183c4;
`;

const LoginModal = ({ open, onHide, children, setAuthentication }) => {
  const [captchaLogin, setCaptchaLogin] = useState(false);
  let form = {};
  return (
    <Modal
      onClose={onHide}
      open={!!open}
      closeIcon={"close"}
      trigger={children}
      size={"tiny"}
      style={{ top: "300px" }}
    >
      <Modal.Header>LazyChun</Modal.Header>
      <Modal.Content>
        {!captchaLogin ? (
          <BasicForm
            width={"100%"}
            items={[
              {
                label: "",
                fieldKey: "username",
                required: true,
                placeholder: ""
              },
              {
                label: "",
                fieldKey: "password",
                type: "password",
                required: true,
                placeholder: ""
              }
            ]}
          >
            {({ form, onValidate, onAddErrors }) => {
              return (
                <div
                  id={"loginBtn"}
                  onClick={() =>
                    onValidate(() => {
                      login(
                        form.username,
                        form.password,
                        setAuthentication
                      ).catch(error => {
                        console.error(error);
                        onAddErrors({ username: "" });
                      });
                    })
                  }
                  style={{ visible: "hidden" }}
                />
              );
            }}
          </BasicForm>
        ) : (
          <BasicForm
            width={"100%"}
            items={[
              {
                label: "|",
                fieldKey: "name",
                required: true,
                placeholder: ""
              },
              {
                label: "",
                fieldKey: "captchaCode",
                required: true,
                placeholder: "",
                renderControl: ({
                  value,
                  onChange,
                  onValidate,
                  form,
                  error,
                  formItemState,
                  onUpdateState,
                  onSetError,
                  setErrors
                }) => (
                  <CaptchaInput
                    value={value}
                    error={error}
                    placeholder={""}
                    onChange={onChange}
                    onSetError={onSetError}
                    state={formItemState}
                    onUpdateState={onUpdateState}
                    onValidate={sendCaptcha => {
                      const name = _get(form, "name");
                      if (!name) {
                        setErrors({ name: "" });
                        return;
                      }
                      if (!(isEmail(name) || isPhone(name))) {
                        setErrors({
                          name: ""
                        });
                        return;
                      }
                      const isNameEmail = isEmail(name);
                      const captchaType = isNameEmail
                        ? CAPTCHA_TYPE.EMAIL
                        : CAPTCHA_TYPE.SMS;
                      const actionType = isNameEmail
                        ? ACTION_TYPE.BindEmail
                        : ACTION_TYPE.BindPhoneNumber;
                      onValidate(
                        () =>
                          sendCaptcha(
                            _get(form, "name"),
                            captchaType,
                            actionType
                          ),
                        ["name"]
                      );
                    }}
                  />
                )
              }
            ]}
          >
            {({ form, onValidate, onAddErrors }) => {
              return (
                <div
                  id={"loginBtn"}
                  onClick={() => {
                    const loginCaptcha = _join(
                      [
                        isEmail(form.name) ? "email" : "phone",
                        form.name,
                        form.captchaCode
                      ],
                      LOGIN_SPLIT_TAG
                    );
                    onValidate(() => {
                      login(
                        loginCaptcha,
                        loginCaptcha,
                        setAuthentication
                      ).catch(error => {
                        console.error(error);
                        onAddErrors({ name: "" });
                      });
                    });
                  }}
                  style={{ visible: "hidden" }}
                />
              );
            }}
          </BasicForm>
        )}
        <FooterBox>
          <CaptchaLoginButton onClick={() => setCaptchaLogin(!captchaLogin)}>
            {captchaLogin ? "" : ""}
          </CaptchaLoginButton>
          <RegisterButton onClick={() => LoginHint.showRegister()}>
            
          </RegisterButton>
        </FooterBox>
      </Modal.Content>
      <Modal.Actions>
        <Button
          content=""
          labelPosition="right"
          icon="checkmark"
          onClick={() => {
            clickEle("loginBtn");
          }}
          positive
        />
      </Modal.Actions>
    </Modal>
  );
};

export default LoginModal;
