import React, { useState, useReducer } from "react";
import _get from "lodash/get";
import LoginModal from "../pages/login/LoginModal";
import RegisterModal from "../pages/login/RegisterModal";

const _LoginHint = ({ onShow, setOnShow }) => {
  const [type, setType] = useState(null);
  const [config, setConfig] = useState({});
  if (!onShow) {
    setOnShow((rConfig, type) => {
      setType(type);
      setConfig(rConfig);
    });
  }

  const onHide = () => {
    setType(null);
  };

  switch (type) {
    case "Login":
      return (
        <LoginModal
          open={type}
          onHide={onHide}
          setAuthentication={config.setAuthentication}
        />
      );
    case "Register":
      return (
        <RegisterModal
          open={type}
          onHide={onHide}
          setAuthentication={config.setAuthentication}
        />
      );
    default:
      return null;
  }
};

class LoginHint {
  static onShow = null;
  static setAuthentication = null;
  static currentUser;

  static setOnShow = onShowFnc => {
    this.onShow = onShowFnc;
  };

  static hintComponent = (
    <_LoginHint onShow={this.onShow} setOnShow={this.setOnShow} />
  );

  static render(setAuthentication, currentUser) {
    if (!this.currentUser) {
      this.currentUser = currentUser;
    }
    if (!this.setAuthentication) {
      this.setAuthentication = setAuthentication;
    }
    return this.hintComponent;
  }

  static show(rConfig) {
    this.onShow(
      { setAuthentication: this.setAuthentication, ...rConfig },
      "Login",
      this.setAuthentication
    );
  }

  static showRegister(rConfig) {
    this.onShow(
      { setAuthentication: this.setAuthentication, ...rConfig },
      "Register"
    );
  }

  static checkLogin(callback, rConfig) {
    if (!this.currentUser) {
      this.show(rConfig);
    } else {
      callback && callback();
    }
  }
}

export default LoginHint;
