import React from "react";
import styled from "styled-components";
import BookHListItem from "../item/BookHListItem";
import FlatList from "../common/FlatList";
import _get from "lodash/get";
const Container = styled.div``;

const BookHListGrid = ({ grid, onGoTo }) => {
  return (
    <Container>
      <FlatList
        items={_get(grid, "items", [])}
        columnsInRow={7}
        itemWidth={180}
        renderItem={({ item }) => {
          return (
            <BookHListItem
              onGoTo={onGoTo}
              item={item}
              key={Math.random().toString()}
            />
          );
        }}
      />
    </Container>
  );
};

export default BookHListGrid;
