import React from "react";
import styled from "styled-components";
import _map from "lodash/map";
import RecommendItem from "./RecommendItem";

const Container = styled.div`
  flex: 1;
  margin-left: 10px;
  background-color: #f0f8fa;
  position: relative;
  padding-top: 40px;
`;

const RankHead = styled.div`
  background-color: #84bf96;
  position: absolute;
  width: 102%;
  display: flex;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 6px;
  top: -10px;
  left: -1%;
  text-shadow: 2px 2px 2px #bbb;
  box-shadow: 0 8px 10px #bbb;
  border-radius: 6px 6px 10px 10px;
`;

const RankHeadText = styled.div`
  font-size: 18px;
  font-weight: bold;
  color: white;
`;

const HomeSwipeGridRecommends = ({ recommends }) => {
  return (
    <Container>
      <RankHead>
        <RankHeadText></RankHeadText>
      </RankHead>
      {_map(recommends, recommend => {
        return (
          <RecommendItem
            index={recommend.index}
            recommend={recommend}
            key={"home_recommend_" + recommend.index}
          />
        );
      })}
    </Container>
  );
};

export default HomeSwipeGridRecommends;
