import React from "react";
import styled from "styled-components";
import SemicircleLabel from "../common/SemicircleLabel";
import FlatList from "../common/FlatList";
import _get from "lodash/get";
import BookItem from "../item/BookItem";
import Space from "../../../common/Space";

const Container = styled.div`
  margin-right: 16px;
`;

const LabelBox = styled.div`
  margin-top: 10px;
  margin-bottom: 4px;
  display: flex;
`;

const BookFlatListGrid = ({ grid, onGoTo }) => {
  const items = _get(grid, "items", []);
  return (
    <Container>
      <LabelBox>
        <SemicircleLabel
          title={grid.title || ""}
          bgColor={grid.labelColor}
        />
      </LabelBox>
      <Space height={"16px"} />
      <FlatList
        items={items}
        columnsInRow={2}
        itemWidth={200}
        gapX={"20px"}
        gapY={"20px"}
        renderItem={({ item }) => {
          return (
            <BookItem
              onGoTo={onGoTo}
              item={item}
              key={Math.random().toString()}
            />
          );
        }}
      />
    </Container>
  );
};

export default BookFlatListGrid;
