import React from "react";
import styled from "styled-components";
import SwipeItems from "../common/SwipeItems";
import _get from "lodash/get";
import ProgramTopGridVRecommends from "./program/ProgramTopGridVRecommends";
import ProgramTopGridHRecommends from "./program/ProgramTopGridHRecommends";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const SwipeBox = styled.div`
  display: flex;
`;

const BottomBox = styled.div``;

const ProgramTopGrid = ({ grid, onGoTo }) => {
  return (
    <Container>
      <SwipeBox>
        <SwipeItems
          width={972}
          height={406}
          onGoTo={onGoTo}
          items={_get(grid, "items", [])}
        />
        <ProgramTopGridVRecommends grid={grid} onGoTo={onGoTo} />
      </SwipeBox>
      <BottomBox>
        <ProgramTopGridHRecommends grid={grid} onGoTo={onGoTo} />
      </BottomBox>
    </Container>
  );
};

export default ProgramTopGrid;
