import React from "react";
import styled from "styled-components";
import _map from "lodash/map";
import _mapValues from "lodash/mapValues";
import _chunk from "lodash/chunk";
import Space from "../../../common/Space";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const RowBox = styled.div`
  display: flex;
  justify-content: space-between;
`;

const useFlatList = ({ items, columnsInRow = 4, gap, gapX, gapY }) => {
  let itemsArr = [];
  if (typeof items === "object") {
    _mapValues(items, value => {
      itemsArr.push(value);
    });
  } else {
    itemsArr = items;
  }
  let itemsLength = itemsArr.length;

  const coverPositionSize = columnsInRow - (itemsLength % columnsInRow);
  let newItems = itemsArr;
  if (coverPositionSize > 0) {
    for (let i = 0; i < coverPositionSize; i++) {
      newItems.push({ isPosition: true, index: Math.random().toString() });
    }
  }
  const rows = _chunk(newItems, columnsInRow);
  return { rows, itemsLength, gX: gapX || gap, gY: gapY || gap };
};

const Position = styled.div`
  width: ${props => props.width}px;
  ${props => (props.isFlexItem ? "flex:1;" : "")}
`;

const FlatList = ({
  items = [],
  gap,
  gapX,
  gapY,
  renderItem,
  itemWidth,
  columnsInRow = 4,
  isFlexItem = false
}) => {
  const { rows, itemsLength, gX, gY } = useFlatList({
    items,
    columnsInRow,
    gap,
    gapX,
    gapY
  });

  return (
    <Container>
      {_map(rows, (row, index) => {
        const isLastRow = index === rows.length - 1;
        return (
          <>
            <RowBox key={Math.random().toString()}>
              {_map(row, (item, itemIndex) => {
                const isLastItem = itemIndex === row.length - 1;
                if (item.isPosition) {
                  return (
                    <>
                      <Position
                        isFlexItem={isFlexItem}
                        width={itemWidth}
                        key={Math.random().toString()}
                      />
                      {!isLastItem && gX && <Space width={gX} />}
                    </>
                  );
                }
                return (
                  <>
                    {renderItem({
                      item,
                      isLastRow: index + 1 === rows.length,
                      isLastOne:
                        index * columnsInRow + itemIndex + 1 === itemsLength
                    })}
                    {!isLastItem && gX && <Space width={gX} />}
                  </>
                );
              })}
            </RowBox>
            {!isLastRow && gY && <Space height={gY} />}
          </>
        );
      })}
    </Container>
  );
};

export default FlatList;
