import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { _getByRank } from "../../../utils/utils";
import { BOOK } from "../../../common/CONSULT";

const Container = styled.div`
  padding: 0 8px;
  cursor: pointer;
  &:hover {
    .cover_image {
      box-shadow: 0 8px 20px #bbb;
    }
    .book_name {
      color: #fb6b84;
    }
  }
`;

const CoverImage = styled.img`
  border-bottom-right-radius: 5px;
  border-top-right-radius: 10px;
`;

const Name = styled.div`
  font-size: 18px;
  font-weight: bold;
  margin-top: 10px;
`;

const Author = styled.div`
  margin-top: 10px;
  color: #666;
  font: 14px / 24px "Microsoft YaHei", , SimSun, tahoma, arial;
`;

const BookItem = ({ item, onGoTo }) => {
  return (
    <Container onClick={() => onGoTo(`${BOOK}${item.id}`)}>
      <CoverImage
        className={"cover_image"}
        width={200}
        height={240}
        src={_get(item, "cover.url")}
      />
      <Name className={"book_name"}>{_get(item, "name")}</Name>
      <Author>
        {_getByRank(item, ["creator.nickname", "creator.username"])}/ 
      </Author>
    </Container>
  );
};

export default BookItem;
