import React, { useState } from "react";
import styled from "styled-components";
import { Dropdown } from "semantic-ui-react";
import Avatar from "../transformable/common/Avatar";
import { AppContext } from "../../../App";
import ModalHint from "../../common/ModalHint";
import LoginHint from "../../common/LoginHint";
import { PERSONAL, PERSONAL_PAGES } from "../../common/CONSULT";
import _get from "lodash/get";
import _isEmpty from "lodash/isEmpty";

const AvatarContainer = styled.div`
  height: 80px;
  display: flex;
  align-items: center;
`;

const LoginAvatar = ({ onGoTo }) => {
  const [showMenu, setShowMenu] = useState(false);
  return (
    <AppContext.Consumer>
      {props => {
        const { currentUser, setAuthentication, logout } = props;
        return (
          <AvatarContainer
            onMouseLeave={() => setShowMenu(false)}
            onBlur={() => setShowMenu(false)}
          >
            <Dropdown
              open={showMenu}
              icon={
                <div onMouseEnter={() => setShowMenu(true)}>
                  <Avatar
                    avatar={_get(currentUser, "avatar")}
                    width={50}
                    size={"big"}
                    cursor={"pointer"}
                  />
                </div>
              }
            >
              <Dropdown.Menu style={{ marginTop: 12, width: 90 }}>
                {!_isEmpty(currentUser) ? (
                  <>
                    <Dropdown.Item
                      onClick={() =>
                        LoginHint.checkLogin(() =>
                          onGoTo(PERSONAL + PERSONAL_PAGES.PROGRAM)
                        )
                      }
                      text=""
                    />
                    <Dropdown.Divider />
                    <Dropdown.Item onClick={() => logout()} text="" />
                  </>
                ) : (
                  <>
                    <Dropdown.Item
                      text=""
                      onClick={() => LoginHint.show()}
                    />
                    <Dropdown.Item
                      text=""
                      onClick={() => LoginHint.showRegister()}
                    />
                  </>
                )}
              </Dropdown.Menu>
            </Dropdown>
          </AvatarContainer>
        );
      }}
    </AppContext.Consumer>
  );
};

export default LoginAvatar;
