import React from "react";
import styled from "styled-components";
import { Grid, GridColumn, GridRow } from "semantic-ui-react";

import { getGridById } from "./grid";
import _map from "lodash/map";
import _isEmpty from "lodash/isEmpty";
import Empty from "../../../common/Empty";

const Container = styled.div`
  margin-bottom: 16px;
`;

const RowHeader = styled.div`
  margin-bottom: 20px;
`;

const TitleWrapper = styled.div`
  display: flex;
  margin-bottom: 16px;
`;

const Title = styled.div`
  font-size: 22px;
  font-weight: bolder;
  text-shadow: 5px 5px 5px #bbb;
`;

const HeaderBottomLine = styled.div`
  background-color: ${props => props.bColor};
  width: 100%;
  height: 14px;
  box-shadow: 0 2px 4px ${props => props.bColor};
  border-radius: 4px;
`;

const RowBody = styled.div`
  min-height: ${props => props.contentMinHeight} px;
  width: 1300px;
  display: flex;
  flex-wrap: wrap;
`;

const GridWrapper = styled.div`
  flex: ${props => props.flex};
`;

const PageRow = ({ row, onGoTo }) => {
  const {
    title,
    hasNoHeader,
    grids,
    hasHeaderLine,
    headerColor = "#cecece"
  } = row;
  return (
    <Container>
      {!hasNoHeader && title && (
        <RowHeader>
          <TitleWrapper>
            <Title>{title}</Title>
          </TitleWrapper>
          {hasHeaderLine && <HeaderBottomLine bColor={headerColor} />}
        </RowHeader>
      )}
      <RowBody columns="equal">
        {!_isEmpty(grids) ? (
          _map(grids, grid => {
            const gridWidth = grid.xs
              ? parseInt(((grid.xs / 24) * 16).toString())
              : 16;
            console.log("gridWidth___________________", gridWidth);
            return (
              <GridWrapper key={"grid___" + grid.index} flex={gridWidth}>
                {getGridById({ grid, onGoTo })}
              </GridWrapper>
            );
          })
        ) : (
          <Empty text={""} />
        )}
      </RowBody>
    </Container>
  );
};

export default PageRow;
