import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import FlatList from "../common/FlatList";
import BookListItem from "../item/BookListItem";

const Container = styled.div``;

const GridTitle = styled.div`
  font-size: 20px;
  font-weight: bolder;
  width: 100%;
  text-align: left;
  margin-bottom: 28px;
  margin-top: 12px;
`;

const BookListGrid = ({ grid, onGoTo }) => {
  const items = _get(grid, "items", []);
  return (
    <Container>
      <GridTitle>{_get(grid, "title", "")}</GridTitle>
      <FlatList
        items={items}
        columnsInRow={1}
        gapY={"14px"}
        renderItem={({ item, isLastOne }) => (
          <BookListItem
            key={Math.random().toString()}
            item={item}
            onGoTo={onGoTo}
            isLastOne={isLastOne}
          />
        )}
      />
    </Container>
  );
};

export default BookListGrid;
