import React from "react";
import styled from "styled-components";
import { BOOK } from "../../../common/CONSULT";

const Container = styled.div`
  cursor: pointer;
  position: relative;
  flex: 1;
  &:hover {
    .blog_name {
      color: #fb6b84;
    }
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
  }
`;

const CoverImage = styled.img`
  height: 198px;
  width: 100%;
`;

const Name = styled.div`
  position: absolute;
  bottom: 10px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  width: 100%;
  text-align: center;
  z-index: 100;
`;

const BookRecommendItem = ({ item, onGoTo }) => {
  const {
    cover: { url },
    name,
    id,
    recommendText
  } = item;
  return (
    <Container onClick={() => onGoTo(`${BOOK}${id}`)}>
      <CoverImage className={"cover_img"} src={url} />
      <Name>{recommendText || name}</Name>
    </Container>
  );
};

export default BookRecommendItem;
