import React, { useState, useEffect } from "react";
import { useQuery } from "@apollo/react-hooks";
import LoadingWrapper from "./LoadingWapper";
import _get from "lodash/get";

const _QueryList = ({
  gql,
  dataKey,
  dataPath,
  countPath,
  showLoading = true,
  renderWhenLoading,
  children,
  searchFields,
  limit,
  currentPage,
  onUpdateCurrentPage,
  queryOptions = {},
  onUpdateCount
}) => {
  const { loading, error, data, refetch, networkStatus } = useQuery(gql, {
    variables: {
      limit,
      ...searchFields,
      offset: (currentPage - 1) * limit
    },
    ...queryOptions
  });
  // Loading
  if (loading && renderWhenLoading && typeof renderWhenLoading === "function") {
    return renderWhenLoading();
  }

  const items =
    dataPath && countPath
      ? _get(data, dataPath, [])
      : _get(data, dataKey + ".items", []);
  const count =
    dataPath && countPath
      ? _get(data, countPath, [])
      : _get(data, dataKey + ".count", 0);

  return (
    <LoadingWrapper
      loading={loading && showLoading}
      count={count}
      onUpdateCount={onUpdateCount}
    >
      {children({
        loading,
        error,
        refetch,
        items,
        count,
        currentPage,
        paginationPages: parseInt((count / limit).toString()) + 1,
        onUpdateCurrentPage
      })}
    </LoadingWrapper>
  );
};

const useQueryList = ({ initCurrentPage }) => {
  const [currentPage, setCurrentPage] = useState(initCurrentPage);
  return { currentPage, setCurrentPage };
};

const QueryList = ({
  gql,
  dataKey,
  dataPath,
  countPath,
  showLoading = true,
  renderWhenLoading,
  children,
  searchFields,
  limit = 10,
  initCurrentPage = 1,
  queryOptions,
  onUpdateCount
}) => {
  const { currentPage, setCurrentPage } = useQueryList({ initCurrentPage });
  return (
    <_QueryList
      gql={gql}
      dataKey={dataKey}
      dataPath={dataPath}
      countPath={countPath}
      children={children}
      renderWhenLoading={renderWhenLoading}
      searchFields={searchFields}
      showLoading={showLoading}
      limit={limit}
      currentPage={currentPage}
      queryOptions={queryOptions}
      onUpdateCurrentPage={setCurrentPage}
      onUpdateCount={onUpdateCount}
    />
  );
};

export default QueryList;
