import React, { useState, useEffect } from "react";

import styled from "styled-components";
import { Input } from "semantic-ui-react";
import Countdown from "react-countdown";
import { Button } from "antd";
import { useSendCaptcha } from "../hooks";

import ToastHint from "../ToastHint";

const Container = styled.div`
  position: relative;
`;

const CaptchaBox = styled.div`
  position: absolute;
  top: 0px;
  bottom: 0px;
  right: 0px;
  display: flex;
  margin: auto;
  border-right: 1px solid
    ${props => (props.isError ? "#e0b4b4" : "rgba(34, 36, 38, 0.15)")};
  align-items: center;
  border-radius: 0.28571429rem;
`;

const Seconds = styled.div`
  color: #cecece;
  font-size: 14px;
  margin-right: 6px;
`;

const renderer = ({ hours, minutes, seconds, completed }) => {
  if (completed) {
    // Render a completed state
    return <Button type={"link"}></Button>;
  } else {
    // Render a countdown
    return <Seconds>{minutes * 60 + seconds}s</Seconds>;
  }
};

const CaptchaButton = ({
  loading,
  showCountDown,
  onComplete,
  onSendCaptcha,
  count,
  onValidate,
  isError
}) => {
  return (
    <CaptchaBox isError={isError}>
      {showCountDown ? (
        <Countdown
          date={Date.now() + 1000 * 120}
          renderer={renderer}
          onComplete={onComplete}
        />
      ) : (
        <Button
          block={loading}
          onClick={() => onValidate(onSendCaptcha)}
          type={"link"}
        >
          {count > 1 ? "" : ""}
        </Button>
      )}
    </CaptchaBox>
  );
};

const CaptchaInput = ({
  value,
  captchaType,
  actionType,
  onChange,
  placeholder,
  onValidate,
  state,
  onUpdateState,
  error,
  onSetError
}) => {
  const { showCountdown = false, count = 0, loading = false } = state;
  const { onSendCaptcha } = useSendCaptcha({
    captchaType,
    actionType,
    onUpdateState,
    count,
    onCompleted: res => {
      if (res && res.success) {
        onUpdateState("showCountdown", true);
        ToastHint.showSuccess("");
      } else {
        onSetError(res && res.message);
        ToastHint.showFailure("");
      }
    },
    onError: error => {
      onSetError("");
      ToastHint.showFailure("");
    }
  });

  return (
    <Input
      defaultValue={value}
      onChange={(e, { name, value }) => {
        onChange(value);
      }}
      maxLength={6}
      placeholder={placeholder}
      action={
        <CaptchaButton
          isError={error}
          loading={loading}
          showCountDown={showCountdown}
          onValidate={onValidate}
          onComplete={() => onUpdateState("showCountdown", false)}
          onSendCaptcha={onSendCaptcha}
          count={count}
          actionPosition={"right"}
        />
      }
    />
  );
};

export default CaptchaInput;
