import React from "react";
import { useQuery } from "@apollo/react-hooks";
import _get from "lodash/get";
import Loading from "../common/Loading";

const PageItem = ({
  id,
  variables,
  itemPath = "",
  children,
  gql,
  autoLoading
}) => {
  variables = id ? { ...variables, id } : variables;
  const { loading, error, data, refetch, networkStatus } = useQuery(gql, {
    variables,
    onError: error => {
      console.log("query error_____________________", error);
    }
  });
  if (autoLoading && loading) {
    return <Loading />;
  }
  if (!data) {
    return null;
  }
  return children({
    loading,
    refetch,
    item: itemPath ? _get(data, itemPath, {}) : data
  });
};

export default PageItem;
