import React from "react";
import { Rating } from "semantic-ui-react";
import styled from "styled-components";
import { PROGRAM } from "../../../../common/CONSULT";

const Container = styled.div`
  cursor: pointer;
  margin-bottom: 10px;
  &:hover {
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
    .program_name {
      color: #fb6b84;
      font-size: 16px;
    }
    .p_star {
      color: yellow !important;
    }
  }
`;

const Cover = styled.div`
  position: relative;
  margin-bottom: 4px;
`;

const CoverImage = styled.img`
  width: 310px;
  height: 200px;
`;

const CoverHint = styled.div`
  position: absolute;
`;

const BottomBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 300px;
`;

const Name = styled.div`
  font-weight: bold;
  font-size: 14px;
`;

const ProgramItem = ({ item, onGoTo }) => {
  const {
    id,
    name,
    averageScore,
    cover: { url }
  } = item;
  return (
    <Container
      onClick={() => {
        onGoTo(`${PROGRAM}` + id);
      }}
    >
      <Cover>
        <CoverImage
          className={"cover_img"}
          width={300}
          height={200}
          src={url}
        />
        <CoverHint />
      </Cover>
      <BottomBox>
        <Name className={"program_name"}>{name}</Name>
        <Rating disabled rating={averageScore} maxRating={5} />
      </BottomBox>
    </Container>
  );
};

export default ProgramItem;
