import React from "react";
import styled from "styled-components";
import PageRows from "./common/PageRows";
const Container = styled.div`
  margin-top: 18px;
`;

const TransformablePage = ({ page, onGoTo }) => {
  return (
    <Container>
      <PageRows page={page} onGoTo={onGoTo} />
    </Container>
  );
};

export default TransformablePage;
