import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import ProgramListItem from "../item/ProgramListItem";
import FlatList from "../common/FlatList";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 60px;
`;

const ProgramListGrid = ({ grid,onGoTo }) => {
  return (
    <Container>
      <FlatList
        columnsInRow={6}
        itemWidth={200}
        items={_get(grid, "items", [])}
        renderItem={({ item }) => {
          return <ProgramListItem onGoTo={onGoTo} item={item} key={Math.random().toString()} />;
        }}
      />
    </Container>
  );
};

export default ProgramListGrid;
