import React, { useEffect } from "react";
import Loading from "./Loading";
import styled from "styled-components";

const Container = styled.div``;

const LoadingWrapper = ({ loading, children, onUpdateCount, count }) => {
  useEffect(() => {
    if (count) {
      onUpdateCount && onUpdateCount(count);
    }
  }, [count]);
  return loading ? (
    <Loading />
  ) : (
    <Container className={"loadingWrapper"}>{children}</Container>
  );
};

export default LoadingWrapper;
