import _get from "lodash/get";
import _isEmpty from "lodash/isEmpty";
import _slice from "lodash/slice";
import _head from "lodash/head";

export const _getByRank = (item, arrPaths = [], defaultValue = "-") => {
    if (_isEmpty(arrPaths)) {
        return defaultValue;
    }
    const value = _get(item, _head(arrPaths));
    if (value) {
        return value;
    }
    return _getByRank(item, _slice(arrPaths, 1, arrPaths.length));
};
