import React, { useState, useEffect } from "react";
import styled from "styled-components";
import QueryList from "./QueryList";
import { Pagination } from "semantic-ui-react";

const Container = styled.div`
  width: 100%;
`;

const ContentBox = styled.div`
  width: 100%;
`;

const PaginationBox = styled.div``;

const PaginationQueryList = ({
  showLoading = true,
  limit = 10,
  initCurrentPage = 1,
  gql,
  dataKey,
  searchFields,
  countPath,
  dataPath,
  renderWhenLoading,
  renderPagination,
  children,
  paginationSize,
  paginationSecondary,
  hidePaginationInSinglePage = false,
  queryOptions,
  onUpdateCount
}) => {
  return (
    <Container>
      <QueryList
        showLoading={showLoading}
        gql={gql}
        limit={limit}
        countPath={countPath}
        dataPath={dataPath}
        dataKey={dataKey}
        initCurrentPage={initCurrentPage}
        searchFields={searchFields}
        queryOptions={queryOptions}
        renderWhenLoading={renderWhenLoading}
        onUpdateCount={onUpdateCount}
      >
        {({
          loading,
          error,
          refetch,
          items,
          count,
          currentPage,
          paginationPages,
          onUpdateCurrentPage
        }) => {
          return (
            <ContentBox>
              {children && children({ loading, error, refetch, items, count })}
              {hidePaginationInSinglePage && paginationPages === 1 ? null : (
                <PaginationBox className={"paginationWrapper"}>
                  {renderPagination ? (
                    renderPagination({})
                  ) : (
                    <Pagination
                      pointing
                      secondary={paginationSecondary}
                      size={paginationSize}
                      activePage={currentPage}
                      totalPages={count / limit}
                      onPageChange={(event, { activePage }) => {
                        onUpdateCurrentPage(activePage);
                      }}
                    />
                  )}
                </PaginationBox>
              )}
            </ContentBox>
          );
        }}
      </QueryList>
    </Container>
  );
};

export default PaginationQueryList;
